import React, { useContext, useEffect } from 'react';
import { Button } from '@mui/material';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import "./css/Featured.css";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import BookContext from '../Context/BookContext';
import {useNavigate } from "react-router-dom";

const ReviewBooks = () => {
    const navigation = useNavigate();
    const Carthandler = (e) => {
        navigation(`/View/${e}/`);
    }
    var settings = {
        dots: false,
        infinite: true,
        autoplaySpeed: 2000,
        pauseOnHover: true,
        slidesToShow: 6,
        slidesToScroll: 1,
        autoplay: true,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: false
                }
            },
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: false
                }
            },
            {
                breakpoint: 700,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    initialSlide: 1
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };

    const context = useContext(BookContext);
    const {  mystery,fetchmystery } = context;
    useEffect(() => {
        fetchmystery()
    }, []);

    return (
        <>
            {
                <Slider {...settings} >
                    {
                        mystery.map(
                            data => (
                                <div key={data._id}>
                                    <div className="Featured">
                                        <div className="card border border-0">
                                            <img src={require(`../../../../storeadmin/public/upload/${data.image}`)} className="img-fluid rounded shadow-lg" alt="..." />
                                            <div className="card-body">
                                                <span className="card-text fw-bolder d-inline-block text-truncate" style={{ maxWidth: '180px' }}>{data.name}</span><br/>
                                                <small className="card-text text-muted"> ${data.price} <del> ${data.price + 2} </del></small><br/>
                                                <Button variant="contained" size='small' sx={{mt:1}} startIcon={<AddShoppingCartIcon />} onClick={() => { Carthandler(data._id) }}>Add Cart</Button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        )
                    }
                </Slider>
            }
        </>
    )
}

export default ReviewBooks