import React from 'react'
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

const NetBank = () => {
    return (
        <>
            <Typography sx={{ m: 5 }}>
                Please enter your Bank details
            </Typography>
            <Box component="form">
                <TextField type='text' fullWidth label="UPI ID" variant="outlined" placeholder='Enter UPI ID' />
                <Button variant="contained" fullWidth sx={{ mt: 5, background: '#ffeb3b', color: 'black', fontWeight: 500 }}>Pay Now</Button>
                <Box sx={{ textAlign: "right" }}>
                    <Button variant="text">Cancel</Button>
                </Box>
            </Box>
        </>
    )
}

export default NetBank