import React, { useContext, useEffect } from 'react'
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import Button from '@mui/material/Button';
import BookContext from '../../Context/BookContext';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import ClearIcon from '@mui/icons-material/Clear';


const Address = () => {
    //select box
    const [Country, setCountry] = React.useState('');
    const [State, setState] = React.useState('');
    const handleChangecountry = (event) => {
        setCountry(event.target.value);
    };
    const handleChangeState = (event) => {
        setState(event.target.value);
    };
    //Store address details
    const handleAddress = async (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const Address = {
            name: data.get('name'),
            last: data.get('last'),
            country: data.get('country'),
            house: data.get('house'),
            apartment: data.get('apartment'),
            town: data.get('town'),
            state: data.get('state'),
            pin: data.get('pin'),
            phone: data.get('phone'),
            gmail: data.get('email'),
        }
        const response = await fetch("http://localhost:5000/api/address/address", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'auth-token': localStorage.getItem('token')
            },
            body: JSON.stringify({ name: Address.name, last: Address.last, country: Address.country, house: Address.house, apartment: Address.apartment, town: Address.town, state: Address.state, pin: Address.pin, phone: Address.phone, gmail: Address.gmail })
        });
        const json = await response.json();
        if (json.error) {
            alert(`${json.error[0].param} : ${json.error[0].msg}`);
        } else {
            alert('Address Inserted Successfully...!')
            fetchaddress();
        }
    }
    // //fetch Address data
    const context = useContext(BookContext);
    const { fetchaddress, address } = context;
    useEffect(() => {
        fetchaddress()
    }, []);

    //handle Delete function
    const handleDeleteAdd = async (id) => {
        const response = await fetch("http://localhost:5000/api/address/deleteaddress", {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({id:id})
        });
        const json = await response.json();
        if (json) {
            alert(`Deleted Successfully....!`);
            fetchaddress();
        }
    }

    return (
        <>
            <Typography variant='body1' color='#283593' sx={{ fontSize: 20, fontWeight: 700, m: 4 }}>BILLING ADDRESS</Typography>
            <Divider />
            <Box sx={{ mb: 10, p: 5 }} component="form" autoComplete="off" id='address-form' onSubmit={handleAddress}>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <TextField fullWidth label="First name" name='name' id="fullWidth" placeholder='Enter Name' required />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField fullWidth label="Last name" name='last' id="fullWidth" placeholder='Enter Last Name' required />
                    </Grid>
                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-autowidth-label">Select Country *</InputLabel>
                            <Select
                                labelId="demo-simple-select-autowidth-label"
                                name='country'
                                value={Country}
                                onChange={handleChangecountry}
                                label="Select Country"
                                required
                            >
                                <MenuItem value="">Select a country / region…</MenuItem>
                                <MenuItem value="Bangladesh">Bangladesh</MenuItem>
                                <MenuItem value="India">India</MenuItem>
                                <MenuItem value="Indonesia">Indonesia</MenuItem>
                                <MenuItem value="Nepal">Nepal</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6}>
                        <TextField fullWidth label="House number and street name " name='house' id="fullWidth" required placeholder='Enter House number and street name' />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField fullWidth label="Apartment, suite, unit, etc." name='apartment' id="fullWidth" required placeholder='Enter Apartment, suite, unit, etc.' />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField fullWidth label="Town / City " id="fullWidth" name='town' required placeholder='Enter Town / City' />
                    </Grid>
                    <Grid item xs={12}>
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-autowidth-label">State *</InputLabel>
                            <Select
                                name='state'
                                id="demo-simple-select-autowidth"
                                value={State}
                                onChange={handleChangeState}
                                label="State"
                                required
                            >
                                <MenuItem value="">Select an MenuItem…</MenuItem>
                                <MenuItem value="Andhra Pradesh">Andhra Pradesh</MenuItem>
                                <MenuItem value="Arunachal Pradesh">Arunachal Pradesh</MenuItem>
                                <MenuItem value="Assam">Assam</MenuItem>
                                <MenuItem value="Bihar">Bihar</MenuItem>
                                <MenuItem value="Chhattisgarh">Chhattisgarh</MenuItem>
                                <MenuItem value="Goa">Goa</MenuItem>
                                <MenuItem value="Gujarat">Gujarat</MenuItem>
                                <MenuItem value="Haryana">Haryana</MenuItem>
                                <MenuItem value="Himachal Pradesh">Himachal Pradesh</MenuItem>
                                <MenuItem value="Jammu and Kashmir">Jammu and Kashmir</MenuItem>
                                <MenuItem value="Jharkhand">Jharkhand</MenuItem>
                                <MenuItem value="Karnataka">Karnataka</MenuItem>
                                <MenuItem value="Kerala">Kerala</MenuItem>
                                <MenuItem value="Ladakh">Ladakh</MenuItem>
                                <MenuItem value="Madhya Pradesh">Madhya Pradesh</MenuItem>
                                <MenuItem value="Maharashtra">Maharashtra</MenuItem>
                                <MenuItem value="Manipur">Manipur</MenuItem>
                                <MenuItem value="Meghalaya">Meghalaya</MenuItem>
                                <MenuItem value="Mizoram">Mizoram</MenuItem>
                                <MenuItem value="Nagaland">Nagaland</MenuItem>
                                <MenuItem value="Odisha">Odisha</MenuItem>
                                <MenuItem value="Punjab">Punjab</MenuItem>
                                <MenuItem value="Rajasthan">Rajasthan</MenuItem>
                                <MenuItem value="Sikkim">Sikkim</MenuItem>
                                <MenuItem value="Tamil Nadu">Tamil Nadu</MenuItem>
                                <MenuItem value="Telangana">Telangana</MenuItem>
                                <MenuItem value="Tripura">Tripura</MenuItem>
                                <MenuItem value="Uttarakhand">Uttarakhand</MenuItem>
                                <MenuItem value="Uttar Pradesh">Uttar Pradesh</MenuItem>
                                <MenuItem value="West Bengal">West Bengal</MenuItem>
                                <MenuItem value="Andaman and Nicobar Islands">Andaman and Nicobar Islands</MenuItem>
                                <MenuItem value="Chandigarh">Chandigarh</MenuItem>
                                <MenuItem value="Dadra and Nagar Haveli">Dadra and Nagar Haveli</MenuItem>
                                <MenuItem value="Daman and Diu">Daman and Diu</MenuItem>
                                <MenuItem value="Delhi">Delhi</MenuItem>
                                <MenuItem value="Lakshadeep">Lakshadeep</MenuItem>
                                <MenuItem value="Pondicherry (Puducherry)">Pondicherry (Puducherry)</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={12}>
                        <TextField fullWidth label="PIN" type='number' name='pin' id="fullWidth" required placeholder='PIN' minLength="6" />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField fullWidth label="Phone" name='phone' id="fullWidth" required placeholder='Phone' />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField fullWidth label="Email address" name='email' type='email' id="fullWidth" required placeholder='Email address' />
                    </Grid>
                    <Grid item xs={12} sx={{ textAlign: "left" }}>
                        <Button variant="contained" color='secondary' type='submit'>SUBMIT</Button>
                    </Grid>
                </Grid>
                {
                    address ?
                        <>
                            <TableContainer component={Paper} sx={{ mt: 5 }}>
                                <Table>
                                    <TableHead>
                                        <TableRow>
                                            <TableCell align="center">Name</TableCell>
                                            <TableCell align="center">Country</TableCell>
                                            <TableCell align="center">House No. and Street Name</TableCell>
                                            <TableCell align="center">Apartment, Suite, Unit</TableCell>
                                            <TableCell align="center">Town</TableCell>
                                            <TableCell align="center">State</TableCell>
                                            <TableCell align="center">Pin</TableCell>
                                            <TableCell align="center">Phone No.</TableCell>
                                            <TableCell align="center">Email ID</TableCell>
                                            <TableCell align="center"> </TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        <TableRow key={address._id}>
                                            <TableCell align="center">{address.name} {address.last}</TableCell>
                                            <TableCell align="center">{address.country}</TableCell>
                                            <TableCell align="center">{address.house}</TableCell>
                                            <TableCell align="center">{address.apartment}</TableCell>
                                            <TableCell align="center">{address.town}</TableCell>
                                            <TableCell align="center">{address.state}</TableCell>
                                            <TableCell align="center">{address.pin}</TableCell>
                                            <TableCell align="center">{address.phone}</TableCell>
                                            <TableCell align="center">{address.gmail}</TableCell>
                                            <TableCell align="center" onClick={() => { handleDeleteAdd(address._id) }}><ClearIcon /></TableCell>
                                        </TableRow>
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        </> : ''
                }
            </Box>
        </>
    )
}

export default Address