import React, { useState, useEffect } from 'react'
import Help from '../../Pages/Help'
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Divider from '@mui/material/Divider';
import Typography from '@mui/material/Typography';

const HelpQuestions = () => {

    const [help, setHelp] = useState([]);

    const fetchHelp = async () => {
        const gethelp = await fetch("http://localhost:5000/api/review/gethelp", {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'auth-token': localStorage.getItem('token')
            },
        });
        setHelp(await gethelp.json());
    }
    useEffect(() => {
        fetchHelp()
    }, []);

    return (
        <>
            <Typography variant='body1' color='#283593' sx={{ fontSize: 20, fontWeight: 700, m: 4, textTransform: "uppercase" }}>Help</Typography>
            <Divider />
            <Box sx={{ p: 5 }}>
                <Grid container spacing={2}>
                    <Grid item xs={4} sx={{ m: -4 }}>
                        <Help />
                    </Grid>
                    <Grid item xs={5}>
                        {
                            help.map((data) => (
                                <>
                                    <Box key={data._id}>
                                        <Paper className='col-9 mt-2' sx={{ p: 3 }}>{data.message}</Paper>
                                        <Paper className='offset-10  mt-2 col-8' sx={{ p: 3 }}>{data.Answer ? data.Answer : 'response send soon...'}</Paper>
                                    </Box>
                                </>
                            ))
                        }
                    </Grid>
                </Grid>
            </Box>
        </>
    )
}
export default HelpQuestions