import React, { useContext, useEffect } from 'react'
import Typography from '@mui/material/Typography';
import InputAdornment from '@mui/material/InputAdornment';
import TextField from '@mui/material/TextField';
import Payicon from '../../images/logo/payicon.png'
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import BookContext from '../../Context/BookContext';


import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';

import Image from '../../images/logo/Axis_Bank_logo_logotype.png'
import Alertmsg from '../../Pages/Alertmsg';
import { useNavigate } from "react-router-dom";

const AddedCard = () => {

    //fetch Address data
    const context = useContext(BookContext);
    const { fetchcard, card } = context;
    useEffect(() => {
        fetchcard()
        // eslint-disable-next-line
    }, []);


    const navigation = useNavigate();
    const [open, setOpen] = React.useState(false);
    const [progress, setProgress] = React.useState(false);
    //Open Fist Dialog
    const handleClickOpen = () => {
        localStorage.setItem("OTP", Math.floor((Math.random() * 1000000) + 1));
        setOpen(true);
        Showalert(`One Time OTP :  ${localStorage.getItem('OTP')}`, 'info');
    };
    //Close Fist Dialog
    const handleClose = () => {
        setOpen(false);
    };
    //timer funcation
    const [seconds, setSeconds] = React.useState(290);
    React.useEffect(() => {
        if (seconds > 0) {
            setTimeout(() => setSeconds(seconds - 1), 1000);
        } else {
            handleClose();
        }
    });
    //Alert Function
    const [alert, Setalert] = React.useState(null);
    const Showalert = (msg, type) => {
        Setalert({
            msg: msg,
            type: type,
            status: true
        })
        setTimeout(() => {
            Setalert(null)
        }, 5000)
    }
    //pay Confirm
    const handleConfirm = (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const OTPdata = {
            otp: data.get('otp')
        }
        if (OTPdata.otp === localStorage.getItem('OTP')) {
            setOpen(false);
            setProgress(true);
            setTimeout(timefunctionprogress, 6000)
        } else {
            setTimeout(timefunctionopen, 3000)
        }
    }
    //Close Using Time Second Dialog
    const timefunctionprogress = async () => {
        setProgress(false);
        const response = await fetch("http://localhost:5000/api/cart/updatecart", {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
                'auth-token': localStorage.getItem('token')
            }
        });
        const json = await response.json();
        if(json){
            navigation('/Dashboard');
        }
    }
    //Close Using Time First Dialog
    const timefunctionopen = () => {
        setOpen(false);
        Showalert(`Invalid Credentials Try Again..!`, 'error');
    }
    //Handle Resend
    const handleResend = () => {
        localStorage.setItem("OTP", Math.floor((Math.random() * 1000000) + 1));
        Showalert(`One Time OTP :  ${localStorage.getItem('OTP')}`, 'info');
    }

    return (
        <>
            {
                card.cardno ?
                    <>
                        <Typography sx={{ m: 10 }} component='div'></Typography>
                        <Box component="form">
                            <TextField
                                fullWidth
                                label="Card Number"
                                value={card.cardno}
                                disabled
                                InputProps={{
                                    endAdornment: <InputAdornment position="start" ><img src={Payicon} alt="Payicon" height={25} width={200} /></InputAdornment>,
                                }}
                            />
                            <TextField type='text' label="Expiry Date/Valid Thru" variant="outlined" sx={{ mr: 2, mt: 2 }} value={card.month} disabled />
                            <TextField type='text' label="Year" variant="outlined" sx={{ m: 2 }} value={card.year} disabled />
                            <TextField type='password' label="CVV/CVC 4-DBC" variant="outlined" sx={{ m: 2 }} value={card.cvv} disabled />
                            <TextField fullWidth type='text' label="Name of the card holder" variant="outlined" value={card.cardhname} disabled />
                            <Button variant="contained" fullWidth sx={{ mt: 5, background: '#ffeb3b', color: 'black', fontWeight: 500 }} onClick={handleClickOpen}>Pay Now</Button>
                            <Box sx={{ textAlign: "right" }}>
                                <Button variant="text">Cancel</Button>
                            </Box>
                            <Alertmsg alert={alert} />
                        </Box>
                    </>
                    : 'Store Card Details'
            }

            {/* //First Dialog */}
            <Dialog open={open} component="form" noValidate autoComplete="off" id='login-form' onSubmit={handleConfirm}>
                <DialogTitle id="alert-dialog-title">
                    {"Verification"}
                </DialogTitle>
                <DialogContent>
                    <Box sx={{ textAlign: 'center' }}>
                        <img src={Image} alt="loding" height={100} width={450} />
                        <Typography component='div' sx={{ m: 3, fontWeight: 700 }}>Enter OTP Send To xxx xxx 1234</Typography>
                        <TextField
                            fullWidth
                            label="Enter OTP"
                            placeholder='OTP'
                            name='otp'
                            InputProps={{
                                endAdornment: <InputAdornment position="start" ><Button variant="text" onClick={handleResend}>Resend OTP</Button></InputAdornment>,
                            }}
                        />
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button type='submit' fullWidth variant="contained">CONFIRM PAYMENT</Button>
                </DialogActions>
                <DialogContent sx={{ textAlign: 'center' }}>
                    <Box>
                        <Typography component='div' sx={{ mb: 3 }}>
                            OTP will expire in {seconds} seconds
                        </Typography>
                        <Typography component='div'>
                            You Can always complete the transaction on your bank's website.
                        </Typography>
                        <Typography sx={{ mt: 3 }} component='div'>
                            DO NOT CLOSE OR REFRESH THE PAGE
                        </Typography>
                    </Box>
                </DialogContent>
            </Dialog>

            {/* //Second Dialog */}

            <Dialog open={progress} component="form" noValidate autoComplete="off" id='login-form'>
                <DialogContent sx={{ textAlign: 'center' }}>
                    <Box>
                        <div className='col-12 mb-3'><span className='text-danger display-5'>Wait till Processing...</span><span id='counttime1'></span></div>
                        <div className='col-12 text-center mt-3 fw-light'><span className='spinner-border spinner-border-sm'></span><span>&nbsp;Processing...</span></div>
                        <div className='col-12 text-center fw-light mt-1'><span>You will be redirected to your BookStore website. It might take a few seconds.</span></div>
                        <Typography sx={{ mt: 3 }} component='div'>
                            DO NOT CLOSE OR REFRESH THE PAGE
                        </Typography>
                    </Box>
                </DialogContent>
            </Dialog>
        </>
    )
}

export default AddedCard