import React, { useContext, useEffect,useState } from 'react'
import "./css/Arrivals.css";
import Divider from '@mui/material/Divider';
import Rating from '@mui/material/Rating';
import Stack from '@mui/material/Stack';
import { Button } from '@mui/material';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import BookContext from '../Context/BookContext'; 
import {useNavigate } from "react-router-dom";
import Alertmsg from '../Pages/Alertmsg';


const Arrivals = () => {
  const navigation = useNavigate();

  //alert Function
  const [alert, Setalert] = useState(null);
  const Showalert = (msg, type) => {
    Setalert({
      msg: msg,
      type: type,
      status: true
    })
    setTimeout(() => {
      Setalert(null)
    }, 2000)
  }
// ********* *//

  const Carthandler = (e) => {
    if(localStorage.getItem('token')){
      navigation(`View/${e}`);
    }else{
      Showalert("Login is required to proceed..!", 'warning');
    }
  }

  function SampleNextArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div
        className={className}
        style={{ ...style, background: "#e7dcdcbd", marginRight: '20px' }}
        onClick={onClick}
      />
    );
  }

  function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div
        className={className}
        style={{ ...style, background: "#e7dcdcbd", marginLeft: '20px' }}
        onClick={onClick}
      />
    );
  }

  var settings = {
    dots: false,
    infinite: true,
    autoplaySpeed: 2000,
    pauseOnHover: true,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    responsive: [
      {
        breakpoint: 1300,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: false
        }
      },
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: false
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };
  const context = useContext(BookContext);
  const {fetchsciencefiction,sciencefiction } = context; 
  useEffect(() => {
    fetchsciencefiction()
  }, []);
  return (
    <>
      <Divider >
        <h3 className='border m-4 p-1'>New Arrivals</h3>
      </Divider>
      {
        <Slider {...settings}>
          {
            sciencefiction.map((data) => (
              <div key={data._id}>
                <div className="card Arrivals">
                  <div className="row">
                    <div className="col-md-4">
                      <img src={require(`../../../../storeadmin/public/upload/${data.image}`)} className="img-fluid rounded-start" alt="..." />
                    </div>
                    <div className="col-md-8 mt-4">
                      <div className="card-body">
                        <h5 className="card-title text-truncate" style={{ maxWidth: "170px" }}>{data.name}</h5>
                        <span className="card-text"> ${data.price} <del> ${data.price + 2} </del></span>
                        <Button variant="contained" size='small' startIcon={<AddShoppingCartIcon />} onClick={() => { Carthandler(data._id) }}>Add Cart</Button>
                        <Stack spacing={1}>
                          <Rating className='rating' name="half-rating-read" value={data.rating} precision={0.5} readOnly />
                        </Stack>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))
          }
        </Slider>
      }
      <Alertmsg alert={alert} />
    </>
  )
}

export default Arrivals