import React, { useContext, useEffect } from 'react'
import Typography from '@mui/material/Typography';
import InputAdornment from '@mui/material/InputAdornment';
import TextField from '@mui/material/TextField';
import Payicon from '../../images/logo/payicon.png'
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Divider from '@mui/material/Divider';
import BookContext from '../../Context/BookContext';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import ClearIcon from '@mui/icons-material/Clear';

const AddCard = () => {
    //addCart
    const handleCardDetails = async (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const Carddata = {
            cardno: data.get('cardno'),
            month: data.get('Date'),
            year: data.get('Year'),
            cvv: data.get('CVV'),
            cardhname: data.get('holder'),
            checkbox: data.get('checkbox')
        }
        const response = await fetch("http://localhost:5000/api/card/card", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'auth-token': localStorage.getItem('token')
            },
            body: JSON.stringify({ cardno: Carddata.cardno, month: Carddata.month, year: Carddata.year, cvv: Carddata.cvv, cardhname: Carddata.cardhname, checkbox: Carddata.checkbox })
        });
        const json = await response.json();
        if (json.error) {
            alert(`${json.error[0].param} : ${json.error[0].msg}`);
        }
        else {
            alert('Card Details Added Successfully...!');
            fetchcard();
        }
    }

    // //fetch Address data
    const context = useContext(BookContext);
    const { fetchcard, card } = context;
    useEffect(() => {
        fetchcard()
    }, []);

       //handle Delete function
       const handleDeletecard = async (id) => {
        const response = await fetch("http://localhost:5000/api/card/deletecard", {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({id:id})
        });
        const json = await response.json();
        if (json) {
            alert(`Deleted Successfully....!`);
            fetchcard();
        }
    }
    return (
        <>
            <Typography variant='body1' color='#283593' sx={{ fontSize: 20, fontWeight: 700, m: 4, textTransform: "uppercase" }}>Store Card details</Typography>
            <Divider />
            <Box sx={{p: 5 }} component="form" noValidate autoComplete="off" id='login-form' onSubmit={handleCardDetails}>

                <TextField
                    name='cardno'
                    type='number'
                    fullWidth
                    label="Card Number"
                    InputProps={{
                        endAdornment: <InputAdornment position="start" ><img src={Payicon} alt="Payicon" height={25} width={200} /></InputAdornment>,
                    }}
                />
                <TextField type='number' name='Date' label="Expiry Date/Valid Thru" variant="outlined" placeholder='Month' sx={{ mr: 2, mt: 2 }} />
                <TextField type='number' name='Year' label="Year" variant="outlined" placeholder='Year' sx={{ m: 2 }} />
                <TextField type='number' name='CVV' label="CVV/CVC 4-DBC" variant="outlined" sx={{ m: 2 }} />
                <TextField fullWidth name='holder' type='text' label="Name of the card holder" variant="outlined" placeholder='Name as on card' />
                <FormControlLabel value="true" control={<Checkbox />} label="General Terms & Conditions for Online Payments" labelPlacement="end" name='checkbox' />
                <Typography sx={{ ml: 5 }}>
                    1. Once a User has accepted these Terms and Conditions, he/ she may register and avail the Services.<br />
                    2. In these Terms and Conditions, the term “Charge Back” shall mean, approved and settled credit card or net banking purchase.<br />
                    3. The Institute and the Payment Service Provider(s) assume no liability whatsoever for any monetary or other damage suffered by the User on account<br />
                </Typography>
                <Box sx={{ textAlign: "center" }}>
                    <Button variant="contained" type='submit' color='secondary' sx={{ mt: 5, fontWeight: 500 }}>Add Card</Button>
                </Box>
            </Box>
            {
                card ?
                    <>
                        <TableContainer component={Paper} sx={{ mt: 5 }}>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell align="center">Name</TableCell>
                                        <TableCell align="center">Card No</TableCell>
                                        <TableCell align="center">Month</TableCell>
                                        <TableCell align="center">Year</TableCell>
                                        <TableCell align="center">CVV</TableCell>
                                        <TableCell align="center"> </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    <TableRow key={card._id}>
                                        <TableCell align="center">{card.cardhname}</TableCell>
                                        <TableCell align="center">{card.cardno}</TableCell>
                                        <TableCell align="center">{card.month}</TableCell>
                                        <TableCell align="center">{card.year}</TableCell>
                                        <TableCell align="center">***</TableCell>
                                        <TableCell align="center" onClick={() => { handleDeletecard(card._id) }}><ClearIcon /></TableCell>
                                    </TableRow>
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </>
                : ''
            }
        </>
    )
}

export default AddCard
