import React, { useState } from 'react'
import { Button, Box, TextField, InputAdornment, IconButton, Checkbox, FormControlLabel } from '@mui/material';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import EmailIcon from '@mui/icons-material/Email';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import Alertmsg from './Alertmsg';
import {useNavigate } from "react-router-dom";

const RegistrationPage = () => {

  const [values, setValues] = React.useState({
    showPassword: false,
  });

  const navigation = useNavigate();

  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const [alert, Setalert] = useState(null);

  const Showalert = (msg, type) => {
    Setalert({
      msg: msg,
      type: type,
      status: true
    })
    setTimeout(() => {
      Setalert(null)
    }, 2000)
  }

  const handleRegistration = async (e) => {
    e.preventDefault();
    const data = new FormData(e.currentTarget);
    const RegistratioData = {
      name: data.get('name'),
      email: data.get('email'),
      password: data.get('password'),
      cpassword: data.get('cpassword'),
      checkbox: data.get('checkbox')
    }
    if (RegistratioData.name && RegistratioData.email && RegistratioData.password && RegistratioData.cpassword && RegistratioData.checkbox) {
      if (RegistratioData.password === RegistratioData.cpassword) {

        const response = await fetch("http://localhost:5000/api/auth/createuser", {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ name:RegistratioData.name, email: RegistratioData.email, password: RegistratioData.password })
        });
        const json = await response.json();
        if (json.errors) {
          Showalert("Password must be atleast 5 Chatracters", 'error');
        } else if (json.error) {
          Showalert(json.error, 'error');
        } else {
          localStorage.setItem('token', json);
          navigation('/Dashboard');
        }
      }else{
        Showalert("Password does not mach", 'error');
      }
    }else{
      Showalert("All Fields Are requied", 'error');
    }
  }

  return (
    <>
      <div className="col-md-9 offset-md-1 border p-5  border-3 rounded-2 text-dark">
        <div className="col rounded text-center g-0 p-2 "><hr />
          <span className='h4 text-center'>Sign in</span><hr />
        </div>
        <Box component="form" noValidate autoComplete="off" id='login-form' onSubmit={handleRegistration}>
          <TextField required fullWidth id='name' name='name' margin='normal' label='Name'
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <AccountCircleIcon color='primary' />
                </InputAdornment>
              )
            }}
          />
          <TextField required fullWidth id='email' name='email' margin='normal' label='Email Address'
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <EmailIcon color='primary' />
                </InputAdornment>
              )
            }}
          />
          <TextField required fullWidth id='password' name='password' margin='normal' label='Password'
            type={values.showPassword ? 'text' : 'password'}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <LockOpenIcon color='primary' />
                </InputAdornment>
              ),

              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    onMouseDown={handleMouseDownPassword}
                    edge="end"
                  >
                    {values.showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              )
            }}
          />
          <TextField required fullWidth id='cpassword' name='cpassword' margin='normal' label='Confirm Password'
            type={values.showPassword ? 'text' : 'password'}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <LockOpenIcon color='primary' />
                </InputAdornment>
              ),

              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    onMouseDown={handleMouseDownPassword}
                    edge="end"
                  >
                    {values.showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              )
            }}
          />
          <FormControlLabel value="true" control={<Checkbox />} label="Agree T&#38;C" labelPlacement="end" name='checkbox' />
          <Box textAlign="center" sx={{ mt: 2, mb: 2 }}>
            <Button variant="contained" color="primary" type='submit' sx={{ mr: 2 }} fullWidth>Sign Up</Button>
          </Box>
        </Box>
      </div>
      <Alertmsg alert={alert} />
    </>
  )
}

export default RegistrationPage