import React from 'react'
import Books from './Books';
import banner_1 from '../images/Banner/banner_1.png';
import banner_2 from '../images/Banner/banner_2.png';
import banner_3 from '../images/Banner/banner_3.png';
import banner_5 from '../images/Banner/banner_5.png';
import banner_6 from '../images/Banner/banner_6.png';
import banner_7 from '../images/Banner/banner_7.png';
import banner_8 from '../images/Banner/banner_8.png';
import AirplanemodeActiveIcon from '@mui/icons-material/AirplanemodeActive';
import SecurityIcon from '@mui/icons-material/Security';
import AutorenewIcon from '@mui/icons-material/Autorenew';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';
import '../Home/Home.css';
const Home = () => {
    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-md-12 col-sm-6">
                    <div className="col">
                        <div id="carouselExampleDark" className="carousel carousel-dark slide" data-bs-ride="carousel">
                            <div className="carousel-indicators">
                                <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                                <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="1" aria-label="Slide 2"></button>
                                <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="2" aria-label="Slide 3"></button>
                                <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="3" aria-label="Slide 4"></button>
                                <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="4" aria-label="Slide 4"></button>
                                <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="5" aria-label="Slide 4"></button>
                                <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="6" aria-label="Slide 4"></button>
                                <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="7" aria-label="Slide 4"></button>
                            </div>
                            <div className="carousel-inner p-4">
                                <div className="carousel-item active" data-bs-interval="10000" sx={{ float: 'center' }}>
                                    <img src={banner_1} className="d-block" alt="..." />
                                </div>
                                <div className="carousel-item" data-bs-interval="2000">
                                    <img src={banner_5} className="d-block" alt="..." />
                                </div>
                                <div className="carousel-item">
                                    <img src={banner_3} className="d-block" alt="..." />
                                </div>
                                <div className="carousel-item">
                                    <img src={banner_2} className="d-block" alt="..." />
                                </div>
                                <div className="carousel-item">
                                    <img src={banner_6} className="d-block" alt="..." />
                                </div>
                                <div className="carousel-item">
                                    <img src={banner_7} className="d-block" alt="..." />
                                </div>
                                <div className="carousel-item">
                                    <img src={banner_8} className="d-block" alt="..." />
                                </div>
                            </div>
                            <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleDark" data-bs-slide="prev">
                                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span className="visually-hidden">Previous</span>
                            </button>
                            <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleDark" data-bs-slide="next">
                                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                <span className="visually-hidden">Next</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <div className="container-fluid mt-4 mb-4 border p-3">
                <div className="row">
                    <div className="col">
                        <div className="row">
                            <div className="col-sm-2 mt-3">
                                <AirplanemodeActiveIcon sx={{ fontSize: 30 }} />
                            </div>
                            <div className="col-sm-10">
                                <div className="row">
                                    <div className="col-10 col-sm-10">
                                        <span className='main'>Free shipping</span><br />
                                        <span className='sub'> Order Over $100</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col">
                        <div className="row">
                            <div className="col-sm-2 mt-3">
                                <SecurityIcon sx={{ fontSize: 30 }} />
                            </div>
                            <div className="col-sm-10">
                                <div className="row">
                                    <div className="col-10 col-sm-10">
                                        <span className='main'> Secure Payment</span><br />
                                        <span className='sub'>  100% Secure Payment</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col">
                        <div className="row">
                            <div className="col-sm-2 mt-3">
                                <AutorenewIcon sx={{ fontSize: 30 }} />
                            </div>
                            <div className="col-sm-10">
                                <div className="row">
                                    <div className="col-10 col-sm-10">
                                        <span className='main'>Easy Returns</span><br />
                                        <span className='sub'> 10 Days Returns</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col">
                        <div className="row">
                            <div className="col-sm-2 mt-3">
                                <AddIcCallIcon sx={{ fontSize: 30 }} />
                            </div>
                            <div className="col-sm-10">
                                <div className="row">
                                    <div className="col-11 col-sm-11">
                                        <span className='main'> 24/7 Support </span><br />
                                        <span className='sub'>  Call Us Anytime</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Books/>
        </div>
    )
}

export default Home 