import React, { useContext, useEffect } from 'react'
import BookContext from '../../Context/BookContext';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import Box from '@mui/material/Box';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import CardContent from '@mui/material/CardContent';

import LinearProgress from '@mui/material/LinearProgress';
import Divider from '@mui/material/Divider';

const OrderedProduct = () => {
    const context = useContext(BookContext);
    const { Mycart, cartdetails, fetchuserdata, Mydetails } = context;
    useEffect(() => {
        //react-hooks/exhaustive-deps
        cartdetails(Mydetails.email, 'wait');
        fetchuserdata();
        //react-hooks/exhaustive-deps
    }, [Mydetails._id]);

    return (
        <>
            <Typography variant='body1' color='#283593' sx={{ fontSize: 20, fontWeight: 700, m: 4, textTransform: "uppercase" }}>Order Product</Typography>
            <Divider />
            {
                Mycart.length > 0 ?
                    <>
                        <TableContainer component={Paper}>
                            <Table sx={{ minWidth: 650 }} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>ITEMS</TableCell>
                                        <TableCell align="center">PAGES</TableCell>
                                        <TableCell align="center">QUANTITY</TableCell>
                                        <TableCell align="center">PRICE</TableCell>
                                        <TableCell align="center">TOTAL</TableCell>
                                        <TableCell align="center">STATUS</TableCell>
                                        <TableCell align="center" sx={{ textTransform: "uppercase" }}>approval</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {Mycart.map((data) => (
                                        <TableRow key={data._id}>
                                            <TableCell align="left">
                                                <Paper sx={{ display: 'flex', boxShadow: 0 }}>
                                                    <CardMedia component="img" sx={{ width: 90, height: 140 }} image={require(`../../../../../storeadmin/public/upload/${data.CartDetails.image}`)}
                                                        alt={data.CartDetails.name} />
                                                    <Box sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }}>
                                                        <CardContent sx={{ flex: '1 0 auto' }}>
                                                            <Typography component="div">
                                                                {data.CartDetails.name}
                                                            </Typography>
                                                            <Typography variant="subtitle1" color="text.secondary" component="div">
                                                                {data.CartDetails.authors}
                                                            </Typography>
                                                        </CardContent>
                                                    </Box>
                                                </Paper>
                                            </TableCell>
                                            <TableCell align="center">{data.CartDetails.pages}</TableCell>
                                            <TableCell align="center">1</TableCell>
                                            <TableCell align="center">{data.CartDetails.price}</TableCell>
                                            <TableCell align="center">{data.CartDetails.price}</TableCell>
                                            <TableCell align="center" sx={{ textTransform: "uppercase" }}>{data.CartDetails.status}</TableCell>
                                            <TableCell align="center" sx={{ textTransform: "uppercase" }}>awaiting<LinearProgress /></TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </>
                    : ''
            }
        </>
    )
}

export default OrderedProduct