import React, { useContext, useEffect } from 'react'
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import BookContext from '../../Context/BookContext';
import Link from '@mui/material/Link';

const Ordersummary = () => {
    const context = useContext(BookContext);
    const { Mycart, cartdetails, fetchuserdata, Mydetails } = context;
    useEffect(() => {
      cartdetails(Mydetails.email,'Cart')
      fetchuserdata()
    }, [Mydetails._id]);
    let a = 0;
    Mycart.map((data) => (
      a = data.CartDetails.price + a
    ))
    return (
        <>
            <Box sx={{ background: "#f5f5f5", p: 5, fontSize: 20, mb:10 }}>
                <Typography variant='body1' color='#283593' sx={{ fontSize: 20, fontWeight: 700 }}>Order Summary</Typography>
                <Typography variant='body1' className='mt-3' >Order No.:15{Math.floor(Math.random() * 10000)+5000}</Typography>
                <Box sx={{ mt: 1, mb: 1 }}>
                    <Typography variant='span'>Merchand Name: </Typography>
                    <Typography variant='span' sx={{ textTransform: "uppercase" }}> The Online BookStore</Typography>
                </Box>
                <Divider />
                <Typography variant='body1' className="mt-2">Amount :  &#36;{a}</Typography>
                <Typography variant='body1' className="mt-2">Processing Fees : 0.00</Typography>
                <Typography variant='body1' className="mt-2">GST : 0.00</Typography>
                <Typography variant='body1' className="mt-2">Total :  &#36;{a}</Typography>
                <Divider />
                <Typography variant='body1' className="mt-2">Visit <Link href="#">The Online BookStore </Link>to know the status of your transaction.</Typography>
            </Box>
        </>
    )
}

export default Ordersummary