import React, { useContext, useEffect } from 'react'
import BookContext from '../../Context/BookContext';
import { useNavigate } from 'react-router-dom';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import Box from '@mui/material/Box';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';

import ClearIcon from '@mui/icons-material/Clear';
import AttachMoneyOutlinedIcon from '@mui/icons-material/AttachMoneyOutlined';
import Divider from '@mui/material/Divider';

const Cartlist = () => {
  const context = useContext(BookContext);
  const { Mycart, cartdetails, fetchuserdata, Mydetails, fetchaddress, address } = context;
  useEffect(() => {
    cartdetails(Mydetails.email, 'Cart');
    fetchuserdata();
    fetchaddress();
  }, [Mydetails._id]);
  let a = 0;
  Mycart.map((data) => (
    a = data.CartDetails.price + a
  ))
  const navigation = useNavigate();
  const handlepaynow = () => {
    if (address) {
      navigation('/PayNow');
    } else {
      alert('Add Address Details....!');
    }
  }
  //handle Delete function
  const handleDelete = async (id) => {
    const response = await fetch("http://localhost:5000/api/cart/deletecart", {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': localStorage.getItem('token')
      },
      body: JSON.stringify({ Cid: id })
    });
    const json = await response.json();
    if (json) {
      alert(`${json.pname} is Deleted Successfully....!`);
      cartdetails(Mydetails.email, 'Cart');
    }
  }
  return (
    <>
      <Typography variant='body1' color='#283593' sx={{ fontSize: 20, fontWeight: 700, m: 4, textTransform: "uppercase" }}>Added CArt</Typography>
      <Divider />
      {
        Mycart.length > 0 ?
          <>
            <TableContainer component={Paper}>
              <Table sx={{ minWidth: 650 }} aria-label="simple table">
                <TableHead>
                  <TableRow>
                    <TableCell>ITEMS</TableCell>
                    <TableCell align="center">PAGES</TableCell>
                    <TableCell align="center">QUANTITY</TableCell>
                    <TableCell align="center">PRICE</TableCell>
                    <TableCell align="center">TOTAL</TableCell>
                    <TableCell align="center">STATUS</TableCell>
                    <TableCell align="center"> </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {Mycart.map((data) => (
                    <TableRow key={data._id}>
                      <TableCell align="left">
                        <Paper sx={{ display: 'flex', boxShadow: 0 }}>
                          <CardMedia component="img" sx={{ width: 90, height: 140 }} image={require(`../../../../../storeadmin/public/upload/${data.CartDetails.image}`)}
                            alt={data.CartDetails.name} />
                          <Box sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }}>
                            <CardContent sx={{ flex: '1 0 auto' }}>
                              <Typography component="div">
                                {data.CartDetails.name}
                              </Typography>
                              <Typography variant="subtitle1" color="text.secondary" component="div">
                                {data.CartDetails.authors}
                              </Typography>
                            </CardContent>
                          </Box>
                        </Paper>
                      </TableCell>
                      <TableCell align="center">{data.CartDetails.pages}</TableCell>
                      <TableCell align="center">1</TableCell>
                      <TableCell align="center">{data.CartDetails.price}</TableCell>
                      <TableCell align="center">{data.CartDetails.price}</TableCell>
                      <TableCell align="center" sx={{ textTransform: "uppercase" }}>{data.CartDetails.status}</TableCell>
                      <TableCell align="center" onClick={() => { handleDelete(data._id) }}><ClearIcon /></TableCell>
                    </TableRow>
                  ))}
                </TableBody>
                <TableBody>
                  <TableRow>
                    <TableCell align="right" colSpan={4}>TOTAL</TableCell>
                    <TableCell align="right">
                      &#36;{a}
                    </TableCell>
                    <TableCell align="right">

                      <Button variant="contained" color='secondary' startIcon={<AttachMoneyOutlinedIcon />} onClick={handlepaynow}>
                        PayNow
                      </Button>

                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>
          </>
          : ''
      }
    </>
  )
}

export default Cartlist