import React, { useContext, useEffect, useState } from 'react';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import CardMedia from '@mui/material/CardMedia';
import BookContext from '../../Context/BookContext';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import ClearIcon from '@mui/icons-material/Clear';
import TextField from '@mui/material/TextField';

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormLabel from '@mui/material/FormLabel';

const PofilePage = () => {
    const [open, setOpen] = useState(false);

    //Handle Dialog Box
    const handleDialog = () => {
        setOpen(true);
    }
    const handleClose = (e) => {
        e.preventDefault();
        setOpen(false);
    }
    //context
    const context = useContext(BookContext);
    const { fetchuserdata, Mydetails, fetchaddress, address } = context;
    useEffect(() => {
        // react-hooks/exhaustive-deps
        fetchuserdata();
        fetchaddress();
    }, []);

    const [gendervalue, setGender] = useState(Mydetails.gender);
    const [namevalue, setName] = useState(Mydetails.name);
    const [emailvalue, setEmail] = useState(Mydetails.email);
    const handleChange = (event) => {
        setGender(event.target.value);
    };
    const handleChangeName = (event) => {
        setName(event.target.value);
    };
    const handleChangeEmail = (event) => {
        setEmail(event.target.value);
    };

    //Handle Update
    const handleUpdate = async (e) => {
        e.preventDefault();
        const response = await fetch("http://localhost:5000/api/auth/updateuser", {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
                'auth-token': localStorage.getItem('token')
            },
            body: JSON.stringify({ name: namevalue, email: emailvalue, gender: gendervalue })
        });
        const json = await response.json();
        if (json.error) {
            alert(`${json.error[0].param} : ${json.error[0].msg}`);
        }
        else {
            alert('Card Details Added Successfully...!');
            fetchuserdata();
        }
    }

    return (
        <>
            <Typography variant='body1' color='#283593' sx={{ fontSize: 20, fontWeight: 700, m: 4, textTransform: "uppercase" }}>Profie</Typography>
            <Divider />
            <Box sx={{ p: 5 }}>
                <Paper>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <Grid sx={{ p: 2 }}>Name : {Mydetails.name}</Grid>
                            <Grid sx={{ p: 2 }}>Email : {Mydetails.email}</Grid>
                            <Grid sx={{ p: 2 }}>Gender : {Mydetails.gender}</Grid>
                            <Grid sx={{ p: 2 }}>Date : {new Date().toDateString(Mydetails.date)}</Grid>
                            <Grid sx={{ p: 2 }}><Button variant="contained" color='secondary' onClick={handleDialog}>Update</Button></Grid>
                        </Grid>
                        <Grid item xs={6}>
                            <Grid sx={{ p: 2, display: 'flex', justifyContent: 'center' }}>
                                <CardMedia sx={{ border: 1, borderRadius: '100%', height: 250, width: 250, borderColor: 'grey.500', p: 1 }} component="img" image={require(`../../images/Upload/${Mydetails.img_URL}`)} />
                            </Grid>
                        </Grid>
                    </Grid>
                </Paper>
                {
                    address ?
                        <Paper className='border' sx={{ mt: 2 }}>
                            <Grid container spacing={2}>
                                <Grid item xs={6}>
                                    <Grid sx={{ p: 2 }}>House No. and Street Name : {address.house}</Grid>
                                    <Grid sx={{ p: 2 }}>Apartment, Suite, Unit : {address.apartment}</Grid>
                                    <Grid sx={{ p: 2 }}>Town : {address.town}</Grid>
                                    <Grid sx={{ p: 2 }}>Phone No.: {address.phone}</Grid>
                                </Grid>
                                <Grid item xs={6}>
                                    <Grid sx={{ p: 2 }}>Country : {address.country}</Grid>
                                    <Grid sx={{ p: 2 }}>State : {address.state}</Grid>
                                    <Grid sx={{ p: 2 }}>Pin : {address.pin}</Grid>
                                    <Grid sx={{ p: 2 }}>Email ID : {address.gmail}</Grid>
                                </Grid>
                            </Grid>
                        </Paper>
                        : ''
                }

            </Box>
            {/* //First Dialog */}
            <Box>
                <Dialog open={open} component="form" id='update-form' onSubmit={handleUpdate}>
                    <DialogTitle id="alert-dialog-title">
                        <Box sx={{ display: 'flex' }}>
                            <Box textAlign='left'>
                                <Typography textAlign='left'>UPDATE</Typography>
                            </Box>
                            <Box className='offset-9'>
                                <Button onClick={handleClose}><ClearIcon /></Button>
                            </Box>
                        </Box>
                    </DialogTitle>
                    <DialogContent>
                        <Grid container spacing={2} sx={{ pr: 2 }}>
                            <Grid item xs={7}>
                                <Grid sx={{ p: 1 }}>
                                    <TextField fullWidth type='text' name='username' label="Name" value={namevalue} onChange={handleChangeName} variant="outlined" required />
                                </Grid>
                                <Grid sx={{ p: 1 }}>
                                    <TextField fullWidth type='email' name='email' label="Email" value={emailvalue} onChange={handleChangeEmail} variant="outlined" required />
                                </Grid>
                                <Grid sx={{ p: 1 }}>
                                    <TextField fullWidth type='text' name='password' label="Password" value="*******" variant="outlined" disabled />
                                </Grid>
                                <Grid sx={{ p: 1 }}>
                                    <FormLabel id="demo-controlled-radio-buttons-group">Gender</FormLabel>
                                    <RadioGroup row value={gendervalue} onChange={handleChange} >
                                        <FormControlLabel value="Female" control={<Radio />} label="Female" />
                                        <FormControlLabel value="Male" control={<Radio />} label="Male" />
                                        <FormControlLabel value="none" control={<Radio />} label="None" />
                                    </RadioGroup>
                                </Grid>
                                <Grid sx={{ p: 1 }}>
                                    <TextField fullWidth name='Date' label="Date" value={new Date().toDateString(Mydetails.date)} variant="outlined" disabled />
                                </Grid>
                            </Grid>
                            <Grid item xs={5}>
                                <Grid sx={{ p: 2, display: 'flex', justifyContent: 'center' }}>
                                    <CardMedia sx={{ border: 1, borderRadius: '100%', height: 200, width: 200, borderColor: 'grey.500', p: 1 }} component="img" image={require(`../../images/Upload/${Mydetails.img_URL}`)} />
                                </Grid>
                                <Grid sx={{ mt: 2 }}>
                                    <TextField
                                        fullWidth
                                        size="small"
                                        variant="standard"
                                        name='imageupload'
                                        accept="image/*"
                                        id="contained-button-file"
                                        multiple type="file" />
                                </Grid>
                            </Grid>
                        </Grid>
                    </DialogContent>
                    <DialogActions>
                        <Button type='submit' fullWidth variant="contained">Update</Button>
                    </DialogActions>
                </Dialog>
            </Box>
        </>
    )
}

export default PofilePage