import React, { useState } from 'react'
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Avatar from '@mui/material/Avatar';
import pic from '../images/Background/undraw_female_avatar_w3jk.svg';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Alertmsg from './Alertmsg';
const Help = () => {
    //alert Function
    const [alert, Setalert] = useState(null);
    const Showalert = (msg, type) => {
        Setalert({
            msg: msg,
            type: type,
            status: true
        })
        setTimeout(() => {
            Setalert(null)
        }, 2000)
    }
    //submit function
    const handleSubmithelp = async (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const UserData = {
            message: data.get('message'),
        }
        const response = await fetch("http://localhost:5000/api/review/help", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'auth-token': localStorage.getItem('token')
            },
            body: JSON.stringify({ message: UserData.message })
        });
        const json = await response.json();
        if (json.error) {
            Showalert(`${json.error[0].param} : ${json.error[0].msg}`, 'error');
        } else {
            Showalert(`Submitted Successfully`, 'success');
            document.getElementById('contact-form').reset();
        }
    }
   
    return (
        <>
            <Box sx={{ display: 'flex', justifyContent: 'center', margin: 5 }} component="form" noValidate autoComplete="off" id='help-form' onSubmit={handleSubmithelp}>
                <Paper elevation={4} sx={{ width: 500, height: 560, }} >
                    <Box sx={{ display: 'flex', justifyContent: 'center', margin: 4 }}>
                        <Avatar
                            alt="...."
                            src={pic}
                            sx={{ width: 200, height: 200, border: 1 }}
                        />
                    </Box>
                    <Box sx={{ padding: 2, textAlign: 'center' }}>
                        <Typography variant="poster">
                            Tell us about the problem you're having and which product you're using.
                        </Typography>
                        <TextField fullWidth placeholder='Enter Your Message' margin='normal' variant='outlined' name='message' label='Your Message' multiline rows={4} helperText='minimum 20 characters' />
                        <Button variant="contained" sx={{ marginTop: 1 }} fullWidth type='submit'>SEND</Button>
                    </Box>
                    <Alertmsg alert={alert} />
                </Paper>
            </Box>
        </>
    )
}

export default Help