import React, { useEffect, useState, useContext } from 'react'
import { useParams } from 'react-router-dom'
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import ShoppingBagIcon from '@mui/icons-material/ShoppingBag';
import Rating from '@mui/material/Rating';
import TextField from '@mui/material/TextField';
import CardHeader from '@mui/material/CardHeader';
import Avatar from '@mui/material/Avatar';
import { red } from '@mui/material/colors';
import CardContent from '@mui/material/CardContent';
import ReviewBooks from "../Books/ReviewBooks";
import BooksReview from '../Books/BooksReview';
import BookContext from '../Context/BookContext';
import { useNavigate } from "react-router-dom";

//image
import image from '../../Components/images/Background/undraw_bibliophile_re_xarc.svg';

const View = () => {
    const { id } = useParams();
    const [cart, setCart] = useState([{ "image": "9781984880994.jpg" }]);
    const [viewReview, setViewReivew] = useState([]);
    const navigation = useNavigate();

    useEffect(() => {
        handleSubmit(id);
        fetchreviewcart();
    }, [id]);


    //context
    const context = useContext(BookContext);
    const { fetchuserdata, Mydetails } = context;
    useEffect(() => {
        fetchuserdata();
    }, [id]);

    const handleSubmit = async (pid) => {
        const response = await fetch("http://localhost:5000/api/books/fetchcart", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ id: pid })
        });
        setCart(await response.json());
    }

    const fetchreviewcart = async () => {
        const viewReview = await fetch("http://localhost:5000/api/review/getreviewcart", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(await { pname: cart.name })
        });
        setViewReivew(await viewReview.json())
    }
    const handleAddcart = async () => {
        const response = await fetch("http://localhost:5000/api/cart/addcart", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ uid: Mydetails.email, pname: cart.name })
        });
        const json = await response.json();
        if (json) {
            alert('cart Added Successfully...!');
        }
    }
    const handleAddcartBuy = () =>{
        handleAddcart();
        navigation('/PayNow');
    }

    const handleReview = async (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const ReviewData = {
            message: data.get('desc'),
            rating: data.get('rating'),
        }
        const response = await fetch("http://localhost:5000/api/review/review", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'auth-token': localStorage.getItem('token')
            },
            body: JSON.stringify({ pname: cart.name, message: ReviewData.message, rating: ReviewData.rating })
        });
        const json = await response.json();
        if (json.error) {
            alert(json.error[0].msg);
        } else {
            document.getElementById('login-form').reset();
            alert('Review Added Successfully...!')
        }
    }
    const a = Mydetails.name.split('');
    return (
        <>
            {
                localStorage.getItem('token') ?
                    <>
                        <Box sx={{ fontFamily: 'Cambria Math', flexGrow: 1, m: 2 }}>

                            <Grid container spacing={2}>
                                <Grid item xs={1}></Grid>
                                <Grid item xs={2} className='border border-end-0 text-center p-1'>
                                    {
                                        cart.image
                                            ?
                                            <img src={require(`../../../../storeadmin/public/upload/${cart.image}`)} className="img-fluid rounded-start p-1 mt-1" alt={cart.image} width="500" height="600" />
                                            :
                                            ''
                                    }
                                </Grid>
                                <Grid item xs={8} className='border border-start-0'>
                                    <Typography variant="h5" sx={{ fontFamily: 'Cambria Math' }}>
                                        {cart.name}
                                    </Typography>
                                    <Typography variant="subtitle2" gutterBottom sx={{ ml: 2, fontFamily: 'Cambria Math', fontSize: 16 }}>
                                        {cart.authors} (Author)
                                    </Typography>
                                    <Typography variant="subtitle2" gutterBottom sx={{ ml: 2, fontFamily: 'Cambria Math' }}>
                                        {cart.title}
                                    </Typography>
                                    <Typography variant="subtitle1" gutterBottom sx={{ ml: 2, fontFamily: 'Cambria Math', fontSize: 16 }}>
                                        Availability: {cart.status}
                                    </Typography>
                                    <Typography variant="subtitle1" gutterBottom sx={{ ml: 2 }}>
                                        <Rating className='rating' name="half-rating-read" value={parseInt(cart.rating)} precision={0.5} readOnly />
                                    </Typography>
                                    <Typography variant="subtitle2" gutterBottom sx={{ ml: 2, fontFamily: 'Cambria Math', fontSize: 16 }}>
                                        FORMAT
                                    </Typography>

                                    <Grid container spacing={2}>
                                        <Grid item xs={3} sx={{ pr: 3 }}>
                                            <Paper elevation={3} sx={{ p: 2 }}>
                                                <Typography variant="body1">
                                                    Hardcover
                                                </Typography>
                                                <Typography variant="body1">
                                                    English
                                                </Typography>
                                                <Typography variant="span">
                                                    ${cart.price}&nbsp;
                                                </Typography>
                                                <Typography variant="span" color='error'>
                                                    <del sx={{ color: "red" }}> ${cart.price + 2} </del>
                                                </Typography>
                                            </Paper>
                                        </Grid>
                                        <Grid item xs={3} sx={{ pr: 3 }}>
                                            <Paper elevation={3} sx={{ p: 2 }}>
                                                <Typography variant="body1">
                                                    Paperback
                                                </Typography>
                                                <Typography variant="body1">
                                                    English
                                                </Typography>
                                                <Typography variant="span">
                                                    ${cart.price + 4}&nbsp;
                                                </Typography>
                                                <Typography variant="span" color='error'>
                                                    <del sx={{ color: "red" }}> ${parseFloat(cart.price + 9).toFixed(2)} </del>
                                                </Typography>
                                            </Paper>
                                        </Grid>

                                        <Grid container spacing={2}>
                                            <Grid item xs={5}>
                                                <Button variant="contained" color="error" sx={{ m: 2 }} size="small" startIcon={<ShoppingBagIcon size='small' />} onClick={handleAddcartBuy}>
                                                    BUY NOW
                                                </Button>
                                                <Button variant="contained" color="error" sx={{ m: 2 }} size="small" startIcon={<AddShoppingCartIcon size='small' />} onClick={handleAddcart}>
                                                    ADD TO CART
                                                </Button>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Typography variant="span">
                                        Related Keywords
                                    </Typography>
                                    <Typography variant="subtitle2" sx={{ fontFamily: 'Cambria Math', ml: 2 }}>
                                        {cart.keyword}
                                    </Typography>
                                </Grid>

                                <Grid item xs={1}></Grid>
                            </Grid>
                            <Grid container spacing={2} sx={{ mt: 1 }}>
                                <Grid item xs={1}></Grid>
                                <Grid item xs={10} className='border'>
                                    <Typography color='error'>About the Book</Typography>
                                    <Typography variant="subtitle1" sx={{ fontFamily: 'Cambria Math', textAlign: 'justify', p: 2 }}>
                                        {cart.desc}
                                    </Typography>
                                </Grid>
                                <Grid item xs={1}></Grid>
                            </Grid>

                            <Grid container spacing={2} sx={{ mt: 1 }}>
                                <Grid item xs={1}></Grid>
                                <Grid item xs={10} className="border">
                                    <BooksReview />
                                </Grid>
                                <Grid item xs={1}></Grid>
                            </Grid>

                            <Grid container spacing={2} sx={{ mt: 1 }}>
                                <Grid item xs={1} ></Grid>
                                <Grid item xs={5} className='border border-end-0' sx={{ p: 2 }}>
                                    <Typography color='error'>Product Details</Typography>
                                    <dl className="row">
                                        <dt className="col-sm-4 mt-4">Publisher</dt>
                                        <dd className="col-sm-8 mt-4">{cart.publisher}</dd>

                                        <dt className="col-sm-4 mt-2">Number of Pages</dt>
                                        <dd className="col-sm-8 mt-2">{cart.pages} Pages</dd>

                                        <dt className="col-sm-4 mt-2">Price</dt>
                                        <dd className="col-sm-8 mt-2">&#36; {cart.price}</dd>

                                        <dt className="col-sm-4 mt-2">Publish Date</dt>
                                        <dd className="col-sm-8 mt-2">{new Date().toDateString(cart.date)}</dd>

                                        <dt className="col-sm-4 mt-2">Language</dt>
                                        <dd className="col-sm-8 mt-2">English</dd>

                                        <dt className="col-sm-4 mt-2">Availability</dt>
                                        <dd className="col-sm-8 mt-2"> {cart.status}</dd>

                                        <dt className="col-sm-4 mt-2">Remaining Quantity</dt>
                                        <dd className="col-sm-8 mt-2"> {cart.quantity}</dd>

                                        <dt className="col-sm-4 mt-2">Book Type</dt>
                                        <dd className="col-sm-8 mt-2"> {cart.variants?cart.variants:'textBook'}</dd>
                                    </dl>
                                </Grid>
                                <Grid item xs={5} className="border " sx={{ pl: 1 }} component="form" noValidate autoComplete="off" id='review-form' onSubmit={handleReview}>
                                    <Typography color='error'>Write a Review</Typography>
                                    <CardHeader
                                        avatar={
                                            <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                                                {a[0]}
                                            </Avatar>
                                        }
                                        title={Mydetails.name}
                                        subheader={new Date().toDateString()}
                                    />
                                    <CardContent>
                                        <TextField fullWidth placeholder='Enter Description'
                                            margin='normal' variant='standard'
                                            label='Description'
                                            name='desc'
                                            multiline rows={3}
                                            helperText='minimum 20 characters'
                                        />
                                    </CardContent>
                                    <Rating name="rating" defaultValue={2} /><br />
                                    <Button variant="contained" color="warning" sx={{ m: 1 }} size="small" type='submit'>
                                        ADD
                                    </Button>
                                </Grid>
                                <Grid item xs={1}></Grid>
                            </Grid>
                            <Grid container spacing={2} sx={{ mt: 1 }}>
                                <Grid item xs={1}></Grid>
                                <Grid item xs={10} className="border" sx={{ p: 2 }}>
                                    <ReviewBooks />
                                </Grid>
                                <Grid item xs={1}></Grid>
                            </Grid>

                            <Grid container spacing={2} sx={{ mt: 1 }} >
                                <Grid item xs={1}></Grid>

                                <Grid item xs={10} className='border'>
                                    <Typography color='error'>Review</Typography>


                                    {
                                        viewReview._id
                                            ?
                                            <>
                                                {
                                                    viewReview.map((data) => (
                                                        <div key={data._id}>
                                                            <CardHeader
                                                                avatar={
                                                                    <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                                                                        R
                                                                    </Avatar>
                                                                }
                                                                title={data.CartDetails.name}
                                                                subheader={new Date().toDateString(data.date)}
                                                            />
                                                            <CardContent>
                                                                <Typography variant="subtitle1" sx={{ fontFamily: 'Cambria Math', textAlign: 'justify', p: 2 }}>
                                                                    {data.message}
                                                                </Typography>
                                                                <Rating name="half-rating-read" value={data.rating} />
                                                            </CardContent>
                                                        </div>
                                                    ))
                                                }
                                            </>
                                            :
                                            <>
                                                <CardHeader
                                                    avatar={
                                                        <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                                                            R
                                                        </Avatar>
                                                    }
                                                    title="Shrimp and Chorizo Paella"
                                                    subheader={new Date().toDateString()}
                                                />
                                                <CardContent>
                                                    <Typography variant="subtitle1" sx={{ fontFamily: 'Cambria Math', textAlign: 'justify', p: 2 }}>
                                                        {cart.desc}
                                                    </Typography>
                                                    <Rating name="half-rating-read" defaultValue={2} />
                                                </CardContent>
                                            </>
                                    }

                                </Grid>

                                <Grid item xs={1}></Grid>
                            </Grid>
                        </Box>
                    </> :
                    <Box sx={{ backgroundImage: `url(${image})`, backgroundRepeat: 'no-repeat', backgroundPosition: 'center', height: '75vh', width: '100%', display: { xs: 'none', sm: 'block' } }}></Box>
            }

        </>
    )
}

export default View