import React, { useState } from 'react'
import cpic from '../images/Background/undraw_mathematics_-4-otb.svg';
import { Grid, Box, Button, TextField, InputAdornment } from '@mui/material';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import EmailIcon from '@mui/icons-material/Email';
import SendIcon from '@mui/icons-material/Send';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import FormControl from '@mui/material/FormControl';
import Alertmsg from './Alertmsg';


const Contact = () => {
    //alert Function
    const [alert, Setalert] = useState(null);
    const Showalert = (msg, type) => {
        Setalert({
            msg: msg,
            type: type,
            status: true
        })
        setTimeout(() => {
            Setalert(null)
        }, 2000)
    }
    //seletbox function
    const [select, setAge] = React.useState('');
    const handleChange = (event) => {
        setAge(event.target.value);
    };
    //submit function
    const handleSubmitcontact = async (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const UserData = {
            name: data.get('name'),
            email: data.get('email'),
            type: data.get('Select'),
            message: data.get('message'),
        }
        const response = await fetch("http://localhost:5000/api/review/contact", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ fullname: UserData.name, email: UserData.email, status: UserData.type, message: UserData.message })
        });
        const json = await response.json();
        if (json.error) {
            Showalert(`${json.error[0].param} : ${json.error[0].msg}`, 'error');
        }else{
            Showalert(`Submitted Successfully`, 'success');
            document.getElementById('contact-form').reset();
        }
    }

    return (
        <Grid container>
            <Grid item lg={7} sm={5} sx={{ border: 0, height: '80vh' }}>
                <Box sx={{ backgroundImage: `url(${cpic})`, backgroundRepeat: 'no-repeat', backgroundPosition: 'center', height: '75vh', width: '100%', display: { xs: 'none', sm: 'block' } }}></Box>
            </Grid>
            <Grid item lg={5} sm={7} xs={12} sx={{ marginTop: '40px' }}>
                <>
                    <div className="col-md-9 offset-md-1">
                        <Box textAlign='center' sx={{ color: '#616161', margin: '20px' }}>
                            <h1>Contact Us</h1>
                        </Box>
                        <Box component="form" noValidate autoComplete="off" id='contact-form' onSubmit={handleSubmitcontact} sx={{ px: 3 }}>
                            <TextField required fullWidth id='name' name='name' margin='normal' label='Full Name'
                                InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <AccountCircleIcon color='disabled' />
                                        </InputAdornment>
                                    )
                                }}
                            />
                            <TextField required fullWidth id='email' name='email' margin='normal' label='Email Address'
                                InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <EmailIcon color='disabled' />
                                        </InputAdornment>
                                    )
                                }}
                            />

                            <FormControl fullWidth>
                                <InputLabel id="demo-simple-select-autowidth-label">Select *</InputLabel>
                                <Select
                                    labelId="demo-simple-select-autowidth-label"
                                    id="demo-simple-select-autowidth"
                                    value={select}
                                    onChange={handleChange}
                                    label="Select"
                                    name='Select'
                                    required
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value='feedback'>Feedback</MenuItem>
                                    <MenuItem value='query'>Query</MenuItem>
                                </Select>
                            </FormControl>


                            <TextField fullWidth placeholder='Enter Your Message' margin='normal' variant='outlined' name='message' label='Your Message' multiline rows={8} helperText='minimum 144 characters' />
                            <Box textAlign="center" sx={{ mt: 2, mb: 2 }}>
                                <Button variant="contained" type='submit' sx={{ mr: 2, background: '#f5f5f5', color: '#616161' }} startIcon={<SendIcon />}>send</Button>
                            </Box>
                        </Box>
                    </div>
                    <Alertmsg alert={alert} />
                </>
            </Grid>
        </Grid>
    )
}

export default Contact