import React, { useState } from 'react'
import { NavLink, useNavigate } from "react-router-dom";
import { Button, Box, TextField, Divider, Checkbox, FormControlLabel } from '@mui/material';
import GoogleIcon from '@mui/icons-material/Google';
import FacebookIcon from '@mui/icons-material/Facebook';
import Alertmsg from './Alertmsg';

const Login = () => {

  const [alert, Setalert] = useState(null);

  const Showalert = (msg, type) => {
    Setalert({
      msg: msg,
      type: type,
      status: true
    })
    setTimeout(() => {
      Setalert(null)
    }, 2000)
  }

  const navigation = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    const data = new FormData(e.currentTarget);
    const UserData = {
      email: data.get('email'),
      password: data.get('password'),
    }
    if (UserData.email && UserData.password) {
      const response = await fetch("http://localhost:5000/api/auth/login", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ email: UserData.email, password: UserData.password })
      });
      const json = await response.json();
      if (json.errors) {
        Showalert("Enter Valid Details...!", 'error');
      } else if (json.error) {
        Showalert(json.error, 'error');
      } else {
        localStorage.setItem('token', json);
        navigation('/Dashboard');
      }
    } else {
      Showalert("All Fields Are requied", 'error');
    }
  }

  return (
    <>
      <div className="col-md-9 offset-md-1 border p-5 border-3 rounded-2 text-dark">
        <Box component="form" noValidate autoComplete="off" id='login-form' onSubmit={handleSubmit}>
          <div className="col rounded text-center g-0 p-2 "><hr />
            <span className='h4 text-center'>Log in</span><hr />
          </div>
          <TextField required fullWidth id='email' name='email' margin='normal' label='Email' />
          <TextField required fullWidth id='password' name='password' margin='normal' label='Password' type="Password" />
          <FormControlLabel value="true" control={<Checkbox />} label="Remember me" labelPlacement="end" />
          <Box textAlign="right" sx={{ mt: 2, mb: 2 }}>
            <Button variant="contained" color="primary" type='submit' sx={{ mr: 2 }}>Login</Button>
            <Button variant="contained" color="error" type='reset'>Reset</Button>
          </Box>
          <NavLink to='/forget' className='text-deco'>Forget Password ?</NavLink>
        </Box>
      </div>
      <Divider sx={{ mt: 3, mb: 1 }}>Sign in With</Divider>
      <Box textAlign="center" sx={{ mt: 3, mb: 1 }}>
        <Button variant="contained" color="error"><GoogleIcon sx={{ mr: 2, color: 'white' }} />Google</Button>
        <Button variant="contained" color="primary" sx={{ ml: 2 }}><FacebookIcon sx={{ mr: 2 }} />Facebook</Button>
      </Box>
      <Alertmsg alert={alert} />
    </>
  )
}

export default Login