import React, { useContext, useEffect } from 'react'
import BookContext from '../../Context/BookContext';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import Box from '@mui/material/Box';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import CardContent from '@mui/material/CardContent';

import PrintIcon from '@mui/icons-material/Print';
import Divider from '@mui/material/Divider';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import ClearIcon from '@mui/icons-material/Clear';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid'

import icon from '../../images/logo/favicon.png';
import './OrderedHistory.css';

const OrderedHistory = () => {
    const context = useContext(BookContext);
    const { Mycart, cartdetails, fetchuserdata, Mydetails, fetchaddress, address } = context;
    useEffect(() => {
        //react-hooks/exhaustive-deps
        cartdetails(Mydetails.email, 'Buy');
        //react-hooks/exhaustive-deps
        fetchuserdata();
        //react-hooks/exhaustive-deps
        fetchaddress();
    }, [Mydetails._id]);
    const [open, setOpen] = React.useState(false);
    const [printdata, setPrintData] = React.useState(false);
    const handleClose = () => {
        setOpen(false);
    }
    const handleprint = async (e) => {
        setOpen(true);
        const response = await fetch("http://localhost:5000/api/cart/printcart", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ pid: e })
        });
        setPrintData(await response.json());
    }
    return (
        <>
            <Typography variant='body1' color='#283593' sx={{ fontSize: 20, fontWeight: 700, m: 4, textTransform: "uppercase" }}>Order History</Typography>
            <Divider />
            {
                Mycart.length > 0 ?
                    <>
                        <TableContainer component={Paper}>
                            <Table sx={{ minWidth: 650 }} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>ITEMS</TableCell>
                                        <TableCell align="center">PAGES</TableCell>
                                        <TableCell align="center">QUANTITY</TableCell>
                                        <TableCell align="center">PRICE</TableCell>
                                        <TableCell align="center">TOTAL</TableCell>
                                        <TableCell align="center">STATUS</TableCell>
                                        <TableCell align="center">PRINT</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {Mycart.map((data) => (
                                        <TableRow key={data._id}>
                                            <TableCell align="left">
                                                <Paper sx={{ display: 'flex', boxShadow: 0 }}>
                                                    <CardMedia component="img" sx={{ width: 90, height: 140 }} image={require(`../../../../../storeadmin/public/upload/${data.CartDetails.image}`)}
                                                        alt={data.CartDetails.name} />
                                                    <Box sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }}>
                                                        <CardContent sx={{ flex: '1 0 auto' }}>
                                                            <Typography component="div">
                                                                {data.CartDetails.name}
                                                            </Typography>
                                                            <Typography variant="subtitle1" color="text.secondary" component="div">
                                                                {data.CartDetails.authors}
                                                            </Typography>
                                                        </CardContent>
                                                    </Box>
                                                </Paper>
                                            </TableCell>
                                            <TableCell align="center">{data.CartDetails.pages}</TableCell>
                                            <TableCell align="center">1</TableCell>
                                            <TableCell align="center">&#36;{data.CartDetails.price}</TableCell>
                                            <TableCell align="center">&#36;{data.CartDetails.price}</TableCell>
                                            <TableCell align="center" sx={{ textTransform: "uppercase" }}>Order Delivered</TableCell>
                                            <TableCell align="center" onClick={() => { handleprint(data._id) }}><PrintIcon color='primary' /></TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>

                        {/* //First Dialog */}
                        <Box>
                            {
                                printdata ? <>
                                    <Dialog open={open} fullScreen>
                                        <DialogTitle id="alert-dialog-title">
                                            <Box sx={{ display: 'flex' }}>
                                                <Box className='offset-11'>
                                                    <Button onClick={handleClose}><ClearIcon /></Button>
                                                </Box>
                                            </Box>
                                        </DialogTitle>
                                        <DialogContent>
                                            <a className="navbar-brand" href="/">
                                                <img src={icon} alt="fav-icon" className='img-fluid rounded pl-5' />
                                            </a>
                                            <Grid container spacing={2} sx={{ mt: 2 }}>
                                                <Grid item xs={12}>
                                                    <Typography variant="body1" gutterBottom component="div">Billing Address/Shipping Address</Typography>
                                                    <Table className='table table-borderless'>
                                                        <TableHead>
                                                            <TableRow>
                                                                <TableCell>House No. and Street Name : {address.house} </TableCell>
                                                                <TableCell>Country : {address.country}</TableCell>
                                                            </TableRow>
                                                        </TableHead>
                                                        <TableBody>
                                                            <TableRow>
                                                                <TableCell>Apartment, Suite, Unit : {address.apartment}</TableCell>
                                                                <TableCell>State : {address.state}</TableCell>
                                                            </TableRow>
                                                            <TableRow>
                                                                <TableCell>Town : {address.town}</TableCell>
                                                                <TableCell>Pin : {address.pin}</TableCell>
                                                            </TableRow>
                                                            <TableRow>
                                                                <TableCell>Phone No.: {address.phone}</TableCell>
                                                                <TableCell>Email ID : {address.gmail}</TableCell>
                                                            </TableRow>
                                                        </TableBody>
                                                    </Table>
                                                </Grid>
                                            </Grid>

                                            <DialogContent container spacing={2} sx={{ mt: 1 }} className="offset-1">
                                                <Grid>Payment Method : Credit Card/Debit Card/NetBanking/UPI/Mobile Wallets</Grid>
                                            </DialogContent>

                                            <Grid container spacing={2} sx={{ mt: 2 }}>
                                                <Grid item xs={12}>
                                                    <Typography variant="body1" gutterBottom component="div">Product Details</Typography>
                                                    <Table className='table table-borderless'>
                                                        <TableHead>
                                                            <TableRow>
                                                                <TableCell>Product</TableCell>
                                                                <TableCell>Price</TableCell>
                                                                <TableCell>Quantity</TableCell>
                                                                <TableCell>Total</TableCell>
                                                            </TableRow>
                                                        </TableHead>
                                                        <TableBody>
                                                            <TableRow>
                                                                <TableCell>
                                                                    <Paper sx={{ display: 'flex', boxShadow: 0 }}>
                                                                        <CardMedia component="img" sx={{ width: 90, height: 140 }} image={require(`../../../../../storeadmin/public/upload/${printdata[0].CartDetails.image}`)}
                                                                            alt={printdata[0].CartDetails.name} />
                                                                        <Box sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }}>
                                                                            <CardContent sx={{ flex: '1 0 auto' }}>
                                                                                <Typography component="div">
                                                                                    {printdata[0].CartDetails.name}
                                                                                </Typography>
                                                                                <Typography variant="subtitle1" color="text.secondary" component="div">
                                                                                    {printdata[0].CartDetails.authors}
                                                                                </Typography>
                                                                            </CardContent>
                                                                        </Box>
                                                                    </Paper>
                                                                </TableCell>
                                                                <TableCell>
                                                                    &#36;{printdata[0].CartDetails.price}
                                                                </TableCell>
                                                                <TableCell>
                                                                    1
                                                                </TableCell>
                                                                <TableCell>
                                                                    &#36;{printdata[0].CartDetails.price}
                                                                </TableCell>
                                                            </TableRow>
                                                        </TableBody>
                                                    </Table>
                                                </Grid>
                                            </Grid>
                                            <Divider/>
                                            <Box sx={{ mt: 2 }}>
                                                Total Paid : &#36;{printdata[0].CartDetails.price}
                                            </Box>
                                        </DialogContent>
                                        <DialogActions>
                                            <Button type='submit' fullWidth variant="contained" onClick={()=>(window.print())} id='printbutton'>Print</Button>
                                        </DialogActions>
                                    </Dialog>
                                </> :
                                    ''
                            }
                        </Box>
                    </>
                    : ''
            }

        </>
    )
}

export default OrderedHistory