import React, { useEffect, useState } from 'react'
import Button from '@mui/material/Button';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import "./search.css";
import { useNavigate } from "react-router-dom";
import Alertmsg from '../Pages/Alertmsg';
import { useParams } from 'react-router-dom'
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';

const Search = () => {

    const [alert, Setalert] = useState(null);
    const navigation = useNavigate();

    const Carthandler = (e) => {
        if (localStorage.getItem('token')) {
            navigation(`/View/${e}`);
        } else {
            Showalert("Login is required to proceed..!", 'warning');
        }
    }
    const Showalert = (msg, type) => {
        Setalert({
            msg: msg,
            type: type,
            status: true
        })
        setTimeout(() => {
            Setalert(null)
        }, 2000)
    }

    const { key } = useParams();
    const [searchdata, SetSearchData] = useState([]);
    const handleSearch = async () => {
        const response = await fetch(`http://localhost:5000/api/review/search/${key}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        SetSearchData(await response.json());
    }
    useEffect(() => {
        handleSearch();
    }, [key]);
    return (
        <>
            <Typography variant='body1' color='#283593' sx={{ fontSize: 20, fontWeight: 700, m: 3, textTransform: "uppercase" }}>Search : {key}</Typography>
            <Divider />
            <div className="container">
                <div className="row" sx={{display: 'flex', alignItems: 'left'}}>
                    {
                        searchdata.map((data) => (
                            <div key={data._id} className="col p-3">
                                <div className="Search overflow-hidden">
                                    <div className="card border border-0">
                                        <img src={require(`../../../../storeadmin/public/upload/${data.image}`)} className="img-fluid rounded shadow-lg" alt="..." />
                                        <div className="card-body">
                                            <span className="card-text fw-bolder d-inline-block text-truncate" style={{ maxWidth: '180px' }}>{data.name}</span><br />
                                            <small className="card-text text-muted"> ${data.price} <del> ${parseFloat((data.price + 2).toFixed(2))} </del></small><br />
                                            <Button variant="contained" size='small' sx={{ mt: 1 }} startIcon={<AddShoppingCartIcon />} onClick={() => { Carthandler(data._id) }}>Add Cart</Button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))
                    }
                </div>
            </div>


            <Paper component="div" sx={{ whiteSpace: 'normal' }}>

            </Paper>
            <Alertmsg alert={alert} />
        </>
    )
}

export default Search