import React, { useEffect } from 'react'
import Divider from '@mui/material/Divider';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import Avatar from '@mui/material/Avatar';
import { red } from '@mui/material/colors';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import CardActions from '@mui/material/CardActions';
import IconButton from '@mui/material/IconButton';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import Rating from '@mui/material/Rating';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const Reviews = () => {
  function SampleNextArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div
        className={className}
        style={{ ...style, background: "#e7dcdcbd", marginRight: '20px' }}
        onClick={onClick}
      />
    );
  }

  function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div
        className={className}
        style={{ ...style, background: "#e7dcdcbd", marginLeft: '20px' }}
        onClick={onClick}
      />
    );
  }

  var settings = {
    dots: false,
    infinite: true,
    autoplaySpeed: 2000,
    pauseOnHover: true,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: false
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };
  const [review, setReview] = React.useState([]);
  const fetchreview = async () => {
    const response = await fetch("http://localhost:5000/api/review/getreview", {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      }
    });
    setReview(await response.json())
  }
  useEffect(() => {
    fetchreview()
    // eslint-disable-next-line
  }, []);
  return (
    <div className='mb-5'>
      <Divider >
        <h3 className='border m-4 p-1'>Reviews</h3>
      </Divider>

      <Slider {...settings}>
        {
          review.map((data) => (
              <div key={data._id}>
                <Card sx={{ maxWidth: 370 }}>
                  <CardHeader
                    avatar={
                      <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                        R
                      </Avatar>
                    }
                    title={data.CartDetails.name}
                    subheader={new Date().toDateString(data.date)}
                  />
                  <CardContent>
                    <Typography variant="body2" color="text.secondary" sx={{mb:1}}>
                      <u>{data.pname}</u> :
                    </Typography>
                    <Typography variant="body2" color="text.secondary" className="overflow-hidden" sx={{textAlign:'justify'}} style={{ height: '9rem' }}>
                      {data.message}
                    </Typography>
                  </CardContent>
                  <CardActions disableSpacing>
                    <IconButton aria-label="add to favorites">
                      <FavoriteIcon />
                    </IconButton>
                    <IconButton aria-label="share">
                      <ShareIcon />
                    </IconButton>
                    <Rating name="half-rating-read" value={data.rating} readOnly sx={{ ml: 8 }} />
                  </CardActions>
                </Card>
              </div>
          ))
        }
      </Slider>
    </div>
  )
}

export default Reviews