import React from 'react'
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
const Forgetpass = () => {
    return (
        <>
            <Box sx={{ display: 'flex', justifyContent: 'center', margin: 5 }}>
                <Paper elevation={4}>
                    <div className="p-5">
                        <div className="col rounded text-center g-0"><hr/>
                            <span className='h4 text-center'>Forget Password</span><hr/>
                            </div>
                            <Box sx={{ padding: 1, textAlign: 'center' }}>
                                <TextField required fullWidth id='email' name='email' margin='normal' label='Email' />
                                <Button variant="contained" sx={{ marginTop: 1 }} fullWidth>Reset Password</Button>
                            </Box>
                        </div>
                </Paper>
            </Box>
        </>
    )
}

export default Forgetpass