import React from 'react'
import { Snackbar, Alert } from '@mui/material';
const Alertmsg = (props) => {
    return (
        props.alert &&
        < Snackbar anchorOrigin={{ vertical: 'top', horizontal: 'right' }} open={props.alert.status}>
            <Alert severity={props.alert.type} sx={{ width: '100%'}} onClose={() => {}}>
                {props.alert.msg}
            </Alert>
        </Snackbar>
    )
}

export default Alertmsg