import React, { useState } from 'react'
import { Grid, Card, Tabs, Tab, Box } from '@mui/material'
import pic from '../images/Background/undraw_reading_time_re_phf7.svg'
import Login from './Login';
import RegistrationPage from './RegistrationPage';
const TabPanel = (props) => {
  const { children, value, index } = props;
  return (
    <div role='tabpanel' hidden={value !== index}>
      {
        value === index && (
          <Box>
            {children}
          </Box>
        )
      }
    </div>
  )
}
const Registration = () => {
  const [value, setValue] = useState(0);
  const handleTabs = (event,newValue)=>{
    setValue(newValue);
  }
  return (
    <>
      <Grid container sx={{marginTop:3}}>
        <Grid item lg={7} sm={5} sx={{ height: '90vh', textAlign: 'center', display:{xs:'none', sm:'block'} }}>
        <Box sx={{backgroundImage: `url(${pic})`, backgroundRepeat: 'no-repeat', backgroundPosition: 'center', height: '75vh', width: '100%', display: { xs: 'none', sm: 'block' } }}></Box>
        </Grid>
        <Grid item lg={5} sm={7} xs={12}>
          <Card sx={{ width: '90%', height: '90%',boxShadow:0}}>
            <Box>
              <Box sx={{marginBottom:2,}}>
                <Tabs indicatorColor='primary' value={value} onChange={handleTabs}>
                  <Tab label='Login' sx={{ textTransform: 'none', fontWeight: 'bold' }}></Tab>
                  <Tab label='Registration' sx={{ textTransform: 'none', fontWeight: 'bold' }}></Tab>
                </Tabs>
              </Box>
              <TabPanel value={value} index={0}>
                <Login/>
              </TabPanel>
              <TabPanel value={value} index={1}>
                <RegistrationPage/>
              </TabPanel>
            </Box>
          </Card>
        </Grid>
      </Grid>
    </>
  )
}

export default Registration