import React from 'react'
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';

import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';



//icons
import CreditCardIcon from '@mui/icons-material/CreditCard';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';

import Card from './Card';
import Ordersummary from './Ordersummary';
import NetBank from './NetBank';
import AddedCard from './AddedCard';


//function sidebar
function TabPanel(props) {
  const { children, value, index } = props;
  return (
    <div>
      {value === index && (
        <Box>
          {children}
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};
const PayNow = () => {

  //sidebar 
  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <>
      <Box sx={{ flexGrow: 1 }}>
        <Box sx={{ background: "#1a237e", color: "white", p: 1 }}>Payment Details</Box>
        <Grid container spacing={2}>
          <Grid item xs={3}>
            <Tabs orientation="vertical" value={value} onChange={handleChange}>
              <Tab icon={<CreditCardIcon />} iconPosition="start" label="Debit/Credit Card" sx={{ paddingLeft: 2, paddingRight: 10 }} />
              <Tab icon={<AccountBalanceWalletIcon />} iconPosition="start" label="Internet Banking" sx={{ paddingLeft: 2, paddingRight: 10 }} />
              <Tab icon={<AccountBalanceWalletIcon />} iconPosition="start" label="Added Card" sx={{ paddingLeft: 2, paddingRight: 16 }} />
            </Tabs>
          </Grid>
          <Grid item xs={5}>
            <TabPanel value={value} index={0}>
              <Card />
            </TabPanel>
            <TabPanel value={value} index={1}>
              <NetBank/>
            </TabPanel>
            <TabPanel value={value} index={2}>
              <AddedCard/>
            </TabPanel>
          </Grid>
          <Grid item xs={4}>
            <Ordersummary />
          </Grid>
        </Grid>
      </Box>
    </>
  )
}

export default PayNow