import React from 'react'
import './Home.css';
import Featured from '../Books/Featured';
import Arrivals from '../Books/Arrivals';
import Child from '../Books/Child';
import Reviews from '../Books/Reviews';
import Disney from '../Books/Disney';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Banner_59 from '../images/Banner/banner_59.jpg';
import Banner_55 from '../images/Banner/banner_55.jpg';
const Books = () => {
    return (
        <>
            <Featured />
            <Arrivals />
            <div className="text-center Banner_55 mt-3 mb-3">
                <Box className='offer'>
                    <TextField label="Email ID" className='textfield' size="small" />
                    <Button variant="contained" color="error"> subscribe </Button>
                </Box>
                <img src={Banner_55} className="rounded img-fluid d-block" alt="..." />
            </div>
            <Child />
            <div className="text-center Banner_55">
                <Box className='subscribe'>
                    <TextField label="Email ID" className='textfield' size="small" />
                    <Button variant="contained" color="error"> subscribe </Button>
                </Box>
                <img src={Banner_59} className="rounded img-fluid d-block" alt="..." />
            </div>
            <Disney/>
            <Reviews />
        </>
    )
}

export default Books