import React, { useState } from 'react'
import BookContext from './BookContext'

const BookState = (props) => {

    const [child, setChild] = useState([]);
    const [disney, setdisney] = useState([]);
    const [sciencefiction, setsciencefiction] = useState([]);
    const [mystery, setmystery] = useState([]);
    const [Mydetails, setMydetails] = useState([]);
    const [Mycart, setMycart] = useState([]);
    const [address, setAddress] = useState([]);
    const [card, setCard] = useState([]);
    const [AudioBook, SetAudioBook] = useState([]);
    //fetch cart Details 
    const cartdetails = async (e,type) => {
        if(localStorage.getItem('token')){
            const response = await fetch('http://localhost:5000/api/cart/displaycart', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ userId: e,type:type})
            });
            setMycart(await response.json());
        }
    }

    //fetch All data
    const fetchuserdata = async () => {
        if(localStorage.getItem('token')){
            const response = await fetch('http://localhost:5000/api/auth/getuser', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    "auth-token": localStorage.getItem('token')
                },
            });
            setMydetails(await response.json());

        }else{
            console.log("Unothorized User Plz Login....!");
        }
    }

    // //fetch Disney data
    const fetchChild = async () => {
        const response = await fetch('http://localhost:5000/api/books/fetchchild', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        setChild(await response.json());
    }


    // //fetch Child data
    const fetchdisney = async () => {
        const response = await fetch('http://localhost:5000/api/books/fetchdisney', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        setdisney(await response.json());
    }

    //fetch Child setsciencefiction
    const fetchsciencefiction = async () => {
        const response = await fetch('http://localhost:5000/api/books/fetchsciencefiction', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        setsciencefiction(await response.json());
    }

    //fetch Child fetchmystery
    const fetchmystery = async () => {
        const response = await fetch('http://localhost:5000/api/books/fetchmystery', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        setmystery(await response.json());
    }

      // //fetch Address data
      const fetchaddress = async () => {
        const response = await fetch('http://localhost:5000/api/address/fetchaddress', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "auth-token": localStorage.getItem('token')
            },
        });
        setAddress(await response.json());
    }

    // //fetch Card data
    const fetchcard = async () => {
        const response = await fetch('http://localhost:5000/api/card/fetchcard', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "auth-token": localStorage.getItem('token')
            },
        });
        setCard(await response.json());
    }

        //fetch Child fetchmystery
        const fetchAudioBook = async () => {
            const response = await fetch('http://localhost:5000/api/books/fetchaudiobook', {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                },
            });
            SetAudioBook(await response.json());
        }

    return (
        <BookContext.Provider value={{fetchAudioBook,AudioBook, fetchcard,card,fetchaddress,address,fetchuserdata, Mydetails, child, fetchChild, disney, fetchdisney, fetchsciencefiction, sciencefiction, mystery, fetchmystery, Mycart,cartdetails }}>
            {props.children}
        </BookContext.Provider>
    )
}

export default BookState