import React, { useContext, useEffect, useState } from 'react';
import icon from '../images/logo/favicon.png';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { NavLink, useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import { Button } from '@mui/material';
import InputBase from '@mui/material/InputBase';
import SearchIcon from '@mui/icons-material/Search';
import DensityMediumIcon from '@mui/icons-material/DensityMedium';
import IconButton from '@mui/material/IconButton';
import MenuItem from '@mui/material/MenuItem';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import LogoutIcon from '@mui/icons-material/Logout';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import BookContext from '../Context/BookContext';
import DashboardIcon from '@mui/icons-material/Dashboard';

const NavBar = () => {
    let history = useNavigate();
    const handleLogout = () => {
        localStorage.removeItem('token');
        history("/");
    }

    //context
    const context = useContext(BookContext);
    const [data, setData] = useState('');
    const { fetchuserdata, Mydetails } = context;
    useEffect(() => {
        // react-hooks/exhaustive-deps
        fetchuserdata()
    }, []);

    const handlekeydown = async (event) => {
        event.preventDefault();
        const Searchdata = event.target.value;
        setData(Searchdata);
        if (Searchdata) {
            history(`/Search/${Searchdata}`);
        } else {
            history("/");
        }
    }
    const handleListNav = (a) =>{
        history(`/Search/${a}`);
    } 

    return (
        <>
            <Box sx={{ flexGrow: 1 }}>
                <nav className="navbar navbar-expand-lg">
                    <div className="container-fluid">
                        <a className="navbar-brand" href="/">
                            <img src={icon} alt="fav-icon" className='img-fluid rounded pl-5' />
                        </a>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarScroll" aria-controls="navbarScroll" aria-expanded="false" aria-label="Toggle navigation">
                            <DensityMediumIcon />
                        </button>
                        <div className="collapse navbar-collapse" id="navbarScroll">
                            <ul className="navbar-nav me-auto my-2 my-lg-0 navbar-nav-scroll">
                                <li className="nav-item">
                                    <Button component={NavLink} to='/' style={({ isActive }) => { return { borderBottom: isActive ? '2px solid #e0e0e0' : '' } }}>Home</Button>
                                </li>
                                <li className="nav-item">
                                    <div className="dropdown me-1">
                                        <Button variant="text" className='dropdown-toggle g-0' id="dropdownMenuOffset" data-bs-toggle="dropdown" aria-expanded="false">Category</Button>
                                        <ul className="dropdown-menu mt-4 p-0">
                                            <Paper sx={{ width: 900, p: 3 }}>
                                                <Grid container spacing={2}>
                                                    <Grid item xs={4}>
                                                        <Button variant="text" onClick={()=>(handleListNav('Disney'))}>Disney</Button > 
                                                        <Button variant="text" onClick={()=>(handleListNav('Child'))}>Child</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Mystery'))}>Mystery</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Thrillers'))}>Thrillers</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Crime'))}>Crime</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Poetry'))}>Poetry</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('History'))}>History</Button >
                                                    </Grid>
                                                    <Grid item xs={4}>
                                                        <Button variant="text" onClick={()=>(handleListNav('Horror'))}>Horror</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Science Fiction'))}>Science Fiction</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Comics & Graphic Novels'))}>Comics &#38; Graphic Novels</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Literature & Fiction'))}>Literature &#38; Fiction</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Historical Fiction'))}>Historical Fiction</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Fantasy'))}>Fantasy</Button >
                                                    </Grid>
                                                    <Grid item xs={4}>
                                                        <Button variant="text" onClick={()=>(handleListNav('Business & Investing'))}>Business &#38; Investing</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Self Development & Hobbies'))}>Self Development &#38; Hobbies</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Science & Technology'))}>Science &#38; Technology</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Travel'))}>Travel</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Cooking & Wine'))}> Cooking &#38; Wine</Button >
                                                        <Button variant="text" onClick={()=>(handleListNav('Biography & Memoir'))}>Biography &#38; Memoir</Button >
                                                    </Grid>
                                                </Grid>
                                            </Paper>
                                        </ul>
                                    </div>
                                </li>
                                <li className="nav-item">
                                    <Button component={NavLink} to='/About' style={({ isActive }) => { return { borderBottom: isActive ? '2px solid #e0e0e0' : '' } }}>About</Button>
                                </li>
                                <li className="nav-item">
                                    <Button component={NavLink} to='/Contact' style={({ isActive }) => { return { borderBottom: isActive ? '2px solid #e0e0e0' : '' } }}>Contact</Button>
                                </li>
                                <li className="nav-item">
                                    {
                                        localStorage.getItem('token') ?
                                            <Button component={NavLink} to='/Help' style={({ isActive }) => { return { borderBottom: isActive ? '2px solid #e0e0e0' : '' } }}>Help</Button>
                                            : ''
                                    }
                                </li>
                            </ul>
                            <form className="d-flex">
                                <ul className="navbar-nav me-auto my-2 my-lg-0 navbar-nav-scroll">
                                    <li className="nav-item border">
                                        <InputBase sx={{ ml: 3, flex: 1 }} placeholder="Search Books" value={data} onChange={handlekeydown} />
                                        <IconButton type="submit" sx={{ ml: 5 }}>
                                            <SearchIcon />
                                        </IconButton>
                                    </li>
                                    <li className="nav-item">
                                        {
                                            localStorage.getItem('token')
                                                ?
                                                <div className='dropdown g-0'>
                                                    &emsp;
                                                    <Button variant="text" className='dropdown-toggle g-0' id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false"><AccountCircleIcon fontSize="small" color="primary" />&emsp;{Mydetails.name}</Button>
                                                    <ul className="dropdown-menu p-2 m-2" aria-labelledby="dropdownMenuButton1">
                                                        <MenuItem component={NavLink} to='/Dashboard'><DashboardIcon fontSize="small" color="primary" />&emsp;Dashboard</MenuItem>
                                                        <MenuItem ><AccountCircleIcon fontSize="small" color="primary"/>&emsp;Profile</MenuItem>
                                                        <MenuItem ><ShoppingCartIcon fontSize="small" color="primary" />&emsp;Cart</MenuItem>
                                                        <MenuItem onClick={handleLogout}><LogoutIcon fontSize="small" color="primary" />&emsp;Logout</MenuItem> 
                                                    </ul>
                                                </div>
                                                :
                                                <>
                                                    &emsp;
                                                    <Button component={NavLink} to='/Login' style={({ isActive }) => { return { borderBottom: isActive ? '2px solid #e0e0e0' : '' } }}>Login/Registration</Button>
                                                </>
                                        }
                                    </li>
                                </ul>
                            </form>
                        </div>
                    </div>
                </nav>
            </Box>
        </>
    )
}

export default NavBar