import React, { useContext, useEffect } from 'react';
import "./css/Arrivals.css";
import Rating from '@mui/material/Rating';
import Stack from '@mui/material/Stack';
import { Button } from '@mui/material';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import BookContext from '../Context/BookContext';
import {useNavigate } from "react-router-dom";

const BooksReview = () => {

    const navigation = useNavigate();
    const Carthandler = (e) => {
        navigation(`/View/${e}/`);
    }

    var settings = {
        dots: false,
        infinite: true,
        autoplaySpeed: 2000,
        pauseOnHover: true,
        slidesToShow: 4,
        slidesToScroll: 1,
        autoplay: true,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                }
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };

    const context = useContext(BookContext);
    const { child, fetchChild } = context;
    useEffect(() => {
        fetchChild()
    }, []);

    return (
        <div>
            { 
                <Slider {...settings}>
                    {
                        child.map((data) => (
                            <div key={data._id}>
                                <div className="card Arrivals">
                                    <div className="row">
                                        <div className="col-md-4">
                                            <img src={require(`../../../../storeadmin/public/upload/${data.image}`)} className="img-fluid rounded-start" alt="..." />
                                        </div>
                                        <div className="col-md-8 mt-4">
                                            <div className="card-body">
                                                <h5 className="card-title text-truncate" style={{ maxWidth: "170px" }}>{data.name}</h5>
                                                <span className="card-text"> ${data.price} <del> ${  data.price + 2} </del></span>
                                                <Button variant="contained" size='small' startIcon={<AddShoppingCartIcon />} onClick={() => { Carthandler(data._id) }}>Add Cart</Button>
                                                <Stack spacing={1}>
                                                    <Rating className='rating' name="half-rating-read" value={parseInt(data.rating)} precision={0.5} readOnly />
                                                </Stack>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))
                    }
                </Slider>
            }
        </div>
    )
}

export default BooksReview