import React, { useContext, useEffect } from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import BookContext from '../Context/BookContext';

//icons
import DashboardIcon from '@mui/icons-material/Dashboard';
import QuestionAnswerIcon from '@mui/icons-material/QuestionAnswer';
import BadgeIcon from '@mui/icons-material/Badge';
import ProductionQuantityLimitsIcon from '@mui/icons-material/ProductionQuantityLimits';
import InventoryIcon from '@mui/icons-material/Inventory';
import FactCheckIcon from '@mui/icons-material/FactCheck';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';

//image
import image from '../../Components/images/Background/undraw_bibliophile_re_xarc.svg';

//import items
import Cartlist from './Profile/Cartlist';
import Address from './Profile/Address';
import AddCard from './Profile/AddCard';
import Profile_Dashboard from './Profile/Profile_Dashboard';
import OrderedProduct from './Profile/OrderedProduct';
import OrderedHistory from './Profile/OrderedHistory';
import HelpQuestions from './Profile/HelpQuestions';
import PofilePage from './Profile/PofilePage';
//import state


//function sidebar
function TabPanel(props) {
  const { children, value, index } = props;

  return (
    <div>
      {value === index && (
        <Box>
          {children}
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};



const Dashboard = () => {


  //context

  const context = useContext(BookContext);
  const { fetchuserdata, Mydetails, NavBarlistHandler } = context;
  useEffect(() => {
    fetchuserdata()
  }, []);


 
  //sidebar 
  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <>
      {
        localStorage.getItem('token') ?
          <Box sx={{ display: 'flex', mb:5 }} >
            <Grid container>
              <Grid xs={6} md={3} className='border border-bottom-0' item>
                <Tabs orientation="vertical" value={value} onChange={handleChange}>
                  <Tab icon={<DashboardIcon />} iconPosition="start" label="Dashboard" sx={{ paddingLeft: 2, paddingRight: 10 }} />
                  <Tab icon={<BadgeIcon />} iconPosition="start" label="Ordered Product" sx={{ paddingLeft: 2, paddingRight: 3 }} />
                  <Tab icon={<ProductionQuantityLimitsIcon />} iconPosition="start" label="Cart List" sx={{ paddingLeft: 0, paddingRight: 10 }} />
                  <Tab icon={<InventoryIcon />} iconPosition="start" label="Order History" sx={{ paddingLeft: 4, paddingRight: 8 }} />
                  <Tab icon={<FactCheckIcon />} iconPosition="start" label="Add Address" sx={{ paddingLeft: 4, paddingRight: 10 }} />
                  <Tab icon={<FactCheckIcon />} iconPosition="start" label="Add Card Details" sx={{ paddingLeft: 4, paddingRight: 6 }} />
                  <Tab icon={<QuestionAnswerIcon />} iconPosition="start" label="Help" sx={{ paddingLeft: 0, paddingRight: 15 }} />
                  <Tab icon={<AdminPanelSettingsIcon />} iconPosition="start" label={Mydetails.name} sx={{ paddingLeft: 1, paddingRight: 8, marginTop: 20 }} />
                </Tabs>
              </Grid>
              <Grid xs={6} md={9} className='border border-start-0 border-bottom-0' item>
                <TabPanel value={value} index={0}>
                  <Profile_Dashboard />
                </TabPanel>
                <TabPanel value={value} index={1}>
                  <OrderedProduct />
                </TabPanel>
                <TabPanel value={value} index={2}>
                  <Cartlist />
                </TabPanel>
                <TabPanel value={value} index={3}>
                  <OrderedHistory />
                </TabPanel>
                <TabPanel value={value} index={4}>
                  <Address />
                </TabPanel>
                <TabPanel value={value} index={5}>
                  <AddCard />
                </TabPanel>
                <TabPanel value={value} index={6}>
                  <HelpQuestions />
                </TabPanel>
                <TabPanel value={value} index={7}>
                 <PofilePage/>
                </TabPanel>
              </Grid>
            </Grid>
          </Box>
          :
          <Box sx={{ backgroundImage: `url(${image})`, backgroundRepeat: 'no-repeat', backgroundPosition: 'center', height: '75vh', width: '100%', display: { xs: 'none', sm: 'block' } }}></Box>
      }
    </>
  )
}

export default Dashboard