import React from 'react'
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import GoogleIcon from '@mui/icons-material/Google';
import YouTubeIcon from '@mui/icons-material/YouTube';
import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import AddLocationIcon from '@mui/icons-material/AddLocation';
import PhoneIcon from '@mui/icons-material/Phone';
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail';
import ViewQuiltIcon from '@mui/icons-material/ViewQuilt';
import WebhookIcon from '@mui/icons-material/Webhook';
import MouseIcon from '@mui/icons-material/Mouse';
import TagIcon from '@mui/icons-material/Tag';
import './footer.css';
const footer = () => {
    return (
        <div>
            <footer>
                <div className="container mt-3">
                    <div className="row footer">
                        <div className="col">
                            <FacebookIcon sx={{ fontSize: 40 }} />
                        </div>
                        <div className="col">
                            <TwitterIcon sx={{ fontSize: 40 }} />
                        </div>
                        <div className="col">
                            <GoogleIcon sx={{ fontSize: 40 }} />
                        </div>
                        <div className="col">
                            <YouTubeIcon sx={{ fontSize: 40 }} />
                        </div>
                        <div className="col">
                            <InstagramIcon sx={{ fontSize: 40 }} />
                        </div>
                        <div className="col">
                            <LinkedInIcon sx={{ fontSize: 40 }} />
                        </div>
                    </div>
                    <div className="row mt-4 mx-5">
                        <div className="col mx-5 footer-text">
                            <h5 className='mb-4'>Explore</h5>
                            <p className="fw-lighter text-muted"> About </p>
                            <p className="fw-lighter text-muted"> Contact </p>
                            <p className="fw-lighter text-muted"> Blog </p>
                            <p className="fw-lighter text-muted"> Help </p>
                        </div>
                        <div className="col mx-5 footer-text">
                            <h5 className='mb-4'>Services</h5>
                            <p className="fw-lighter text-muted"><ViewQuiltIcon/> Web Design</p>
                            <p className="fw-lighter text-muted"><WebhookIcon/> Web Development</p>
                            <p className="fw-lighter text-muted"><MouseIcon/> Pay Per Click Marketing</p>
                            <p className="fw-lighter text-muted"><TagIcon/> Social Media Marketing</p>
                        </div>
                        <div className="col mx-5 footer-text">
                            <h5 className='mb-4'>Contact</h5>
                            <p className="fw-lighter text-muted"><AddLocationIcon/> 154, Grandst New York</p>
                            <p className="fw-lighter text-muted">&emsp;&emsp;New York 10013</p>
                            <p className="fw-lighter text-muted"><PhoneIcon/> (+1) 846 760 5363</p>
                            <p className="fw-lighter text-muted"><AlternateEmailIcon/> info@theonlinebookstore</p>
                        </div>
                    </div>
                    <p className='mt-1 footer'>&#169; copyright 2022 by <b>The Online BookStore&#174;</b> | Privacy Policy</p>
                </div>
            </footer>
        </div>
    )
}

export default footer